import { ObjectType } from '../types';

interface WatchMapValue {
  [key: string | number | symbol]: Set<(newVal: any, oldVal: any) => void>;
}

const watchList: Map<ObjectType, WatchMapValue> = new Map();

export function watch<ObjType, K extends keyof ObjType>(
  watchObj: ObjType,
  key: K,
  callback: (newVal: ObjType[K], oldVal: ObjType[K]) => void,
) {
  if (!watchList.has(watchObj)) {
    const set = new Set<(newVal: ObjType[K], oldVal: ObjType[K]) => void>();
    watchList.set(watchObj, { [key]: set });
  }
  const map = watchList.get(watchObj);
  if (map && !map[key]) {
    map[key] = new Set<(newVal: ObjType[K], oldVal: ObjType[K]) => void>();
  }
  watchList.get(watchObj)?.[key].add(callback);
  let currentVal = watchObj[key];
  Object.defineProperty(watchObj, key, {
    get: () => {
      return currentVal;
    },
    set: (newVal: ObjType[K]) => {
      const oldVal = currentVal;
      currentVal = newVal;
      if (newVal !== oldVal) {
        watchList.get(watchObj)?.[key].forEach((item) => {
          item(newVal, oldVal);
          // callback(newVal, oldVal);
        });
      }
    },
  });
}

export function removeWatch<ObjType>(watchObj: ObjType) {
  if (watchList.has(watchObj)) {
    watchList.delete(watchObj);
  }
}
