import Player from '..';
import * as THREE from 'three';
import { Clip } from '../../common/types/clip';
import Painter from '../painter';
import { removeWatch } from '../../common/utils/watch';

export abstract class ClipBase<T extends Clip = Clip> {
  private isAttachToPainter: boolean = false;

  /**
   * 用于控制clip的事件回调
   */
  public isEntered: boolean = false;

  public player: Player;
  public painter: Painter;
  public sprite?: THREE.Mesh;
  public config: T;
  public texture?: THREE.Texture;

  constructor(config: T, player: Player) {
    this.config = config;
    this.player = player;
    this.painter = player.painter;
  }

  public abstract init(): Promise<void>;

  public play() {}

  public pause() {}

  public show() {
    if (this.sprite) {
      this.sprite.visible = true;
    }
  }

  public hide() {
    if (this.sprite) {
      this.sprite.visible = false;
    }
  }

  public updatePlaybackRate() {}

  public getOriginalDuration() {
    return this.config.playRange.end - this.config.playRange.start;
  }

  public addToPainter() {
    if (!this.isAttachToPainter && this.sprite && this.config.id) {
      this.isAttachToPainter = true;
      this.player.painter.add(this.sprite, this.config.id);
    }
  }

  public removeFromPainter() {
    if (this.isAttachToPainter && this.config.id) {
      this.isAttachToPainter = false;
      this.player.painter.remove(this.config.id);
    }
  }

  /**
   * 首次进入clip时调用，不会继续调用onUpdate
   */
  public async onEnter(isManual: boolean) {}

  /**
   * 离开clip时调用
   */
  public async onLeave(isManual: boolean) {}

  /**
   * 播放器时间改变并且clip在有效范围内调用
   */
  public async onUpdate(isManual: boolean) {}

  /**
   * 销毁函数
   */
  public async destroy() {
    removeWatch(this.config);
    this.removeFromPainter();
  }
}
