import React, { useCallback } from 'react';
import { ClipRenderInfo, ImageClip } from '../../../../../common/types/clip';
import { EffectType } from '../../../../../common/types/player';
import EffectSelector from '../../../../../common/components/EffectSelector';
import FitSelector from '../../../../../common/components/FitSelector';
import { ConfigPanelIconContainer } from '../../../../../common/styleds';

type Props = {
  data: ImageClip;
  onChange(data: Partial<ImageClip>): void;
};

const ImagePanel: React.FC<Props> = ({ data, onChange }) => {
  const handleEffectChange = useCallback((value: EffectType) => {
    onChange({
      effect: value,
    });
  }, [onChange]);
  const handleFitChange = useCallback((value: ClipRenderInfo) => {
    onChange({
      positionInRender: {
        ...data.positionInRender,
        ...value,
      },
    });
  }, [data.positionInRender, onChange]);
  return (
    <ConfigPanelIconContainer>
      <EffectSelector
        value={data.effect}
        onChange={handleEffectChange}
      />
      <FitSelector
        clip={data}
        onChange={handleFitChange}
      />
    </ConfigPanelIconContainer>
  );
};

export default ImagePanel;
