import React, { useEffect } from 'react';
import { TEXT_DEFAULT_DURATION } from '../../../../../common/configs/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { TextClip } from '../../../../../common/types/clip';
import { getPixelRatio, getTextClipSize } from '../../../../../common/utils';

type Props = {
  data: TextClip;
  onLoaded(data: TextClip): void;
};

const TextCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    const { width, height } = getTextClipSize(data);
    onLoaded({
      ...data,
      positionInTimeline: {
        ...data.positionInTimeline,
        duration: TEXT_DEFAULT_DURATION,
      },
      positionInRender: {
        ...data.positionInRender,
        width: width / getPixelRatio(),
        height: height / getPixelRatio(),
      },
      originalWidth: width,
      originalHeight: height,
    });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <StyledAssetCard>
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default TextCard;
