import { Upload } from 'antd';
import { UploadChangeParam } from 'antd/lib/upload';
import { UploadFile } from 'antd/lib/upload/interface';
import React, { useCallback, useState } from 'react';
import { AssetCardContainer, AssetWhole, Radius10Button, SubMenuButton, SubMenuContainer } from '../../../../../common/styleds';
import { AssetType, AudioAsset, GifAsset, ImageAsset, VideoAsset } from '../../../../../common/types/asset';
import AssetCard from '../../../AssetCard';

const MinePanel: React.FC = () => {
  const [videoList, setVideoList] = useState<VideoAsset[]>([]);
  const [imageList, setImageList] = useState<(ImageAsset | GifAsset)[]>([]);
  const [audioList, setAudioList] = useState<AudioAsset[]>([]);
  const handleBeforeUpload = useCallback(() => {
    return false;
  } ,[]);
  const handleFileChange = useCallback((info: UploadChangeParam<UploadFile<any>>) => {
    const file = info.file as unknown as File;
    const fileType = file.type;
    if (fileType?.includes('video/')) {
      const asset: VideoAsset = {
        name: file.name,
        type: AssetType.VIDEO,
        url: URL.createObjectURL(file),
      };
      setVideoList([ ...videoList, asset ]);
    }
    if (fileType?.includes('image')) {
      let asset = {
        name: file.name,
        type: AssetType.IMAGE,
        url: URL.createObjectURL(file),
      } as ImageAsset;
      if (fileType.includes('image/gif')) {
        asset = {
          name: file.name,
          type: AssetType.GIF,
          url: URL.createObjectURL(file),
        } as GifAsset;
      }
      setImageList([ ...imageList, asset ]);
    }
    if (fileType?.includes('audio')) {
      const asset: AudioAsset = {
        name: file.name,
        type: AssetType.AUDIO,
        url: URL.createObjectURL(file),
      };
      setAudioList([ ...audioList, asset ]);
    }
  }, [audioList, imageList, videoList]);
  return (
    <>
      <SubMenuContainer>
        <SubMenuButton className="active">
          全部
        </SubMenuButton>
        <Upload
          beforeUpload={handleBeforeUpload}
          onChange={handleFileChange}
          multiple={false}
          accept="video/*,audio/*,image/png,image/jpeg,image/jpg,image/gif"
          showUploadList={false}
        >
          <Radius10Button style={{ color: '#7B9FD6' }}>
            +
          </Radius10Button>
        </Upload>
      </SubMenuContainer>
      {
        videoList.map((item) => {
          return (
            <AssetWhole key={item.name}>
              <AssetCardContainer>
                <AssetCard data={item} />
              </AssetCardContainer>
              <div className="asset-title">{item.name}</div>
            </AssetWhole>
          );
        })
      }
      {
        audioList.map((item) => {
          return (
            <AssetWhole key={item.name}>
              <AssetCardContainer>
                <AssetCard data={item} />
              </AssetCardContainer>
              <div className="asset-title">{item.name}</div>
            </AssetWhole>
          );
        })
      }
      {
        imageList.map((item) => {
          return (
            <AssetWhole key={item.name}>
              <AssetCardContainer>
                <AssetCard data={item} />
              </AssetCardContainer>
              <div className="asset-title">{item.name}</div>
            </AssetWhole>
          );
        })
      }
    </>
  );
};

export default MinePanel;
