import {
  AssetType,
  AudioAsset,
  GifAsset,
  ImageAsset,
  TextAsset,
  TransitionAsset,
  VideoAsset,
  LottieAsset,
} from '../common/types/asset';
import { AUDIOS } from './audios';
import { GIFS } from './gifs';
import { IMAGES } from './images';
import { TEXTS } from './texts';
import { TRANSITIONS } from './transitions';
import { VIDEOS } from './videos';
import { LOTTIES } from './lotties';

export const ASSET = {
  videos: VIDEOS.map((video) => ({ type: AssetType.VIDEO, ...video })) as VideoAsset[],
  images: IMAGES.map((image) => ({ type: AssetType.IMAGE, ...image })) as ImageAsset[],
  audios: AUDIOS.map((audio) => ({ type: AssetType.AUDIO, ...audio })) as AudioAsset[],
  transitions: TRANSITIONS.map((transition) => ({ type: AssetType.TRANSITION, ...transition })) as TransitionAsset[],
  gifs: GIFS.map((gif) => ({ type: AssetType.GIF, ...gif })) as GifAsset[],
  texts: TEXTS.map((text) => ({ type: AssetType.TEXT, ...text })) as TextAsset[],
  lotties: LOTTIES.map((lottie) => ({ type: AssetType.LOTTIE, ...lottie })) as LottieAsset[],
};
