import { SvgClip } from '../../common/types/clip';
import { ClipBase } from './base';
import * as THREE from 'three';
import { svgToCanvas } from '../../common/utils';
import { watch } from '../../common/utils/watch';

export class Svg extends ClipBase<SvgClip> {
  private canvas: HTMLCanvasElement = document.createElement('canvas');
  private context?: CanvasRenderingContext2D | null;

  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.MeshBasicMaterial;

  public async init() {
    this.initCanvas();
    this.initRender();
    this.watchChange();
  }

  private watchChange() {
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
  }

  private initCanvas() {
    const { width, height } = this.painter.getRealSize();
    this.canvas.width = width;
    this.canvas.height = height;
    this.context = this.canvas.getContext('2d');
  }

  private initRender() {
    const { player: { width, height } } = this;
    this.geometry = new THREE.PlaneGeometry(width, height);
    this.material = new THREE.MeshBasicMaterial();
    this.material.map = new THREE.CanvasTexture(this.canvas);
    this.material.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
  }

  public async onUpdate() {
    if (this.material?.map) {
      const { start, end } = this.config.positionInTimeline;
      const { player: { currentTime } } = this;
      const { width, height } = this.painter.getRealSize();
      const svg = this.config.func({ start, end, currentTime, width, height });
      await svgToCanvas(svg, this.canvas, this.context);
      this.material.map.needsUpdate = true;
    }
  }

  public async destroy() {
    super.destroy();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}
