import { ClipBase } from './base';
import { calcUVMap, Shader } from '../painter/shader';
import effects from '../effects';
import * as THREE from 'three';
import { VideoClip } from '../../common/types/clip';
import { watch } from '../../common/utils/watch';

export class Video extends ClipBase<VideoClip> {
  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.ShaderMaterial;
  public texture?: THREE.VideoTexture;
  private video?: HTMLVideoElement;
  public playbackRate = 1;
  private isPlaying = false;

  public async init() {
    await this.initRender();
    this.updatePlaybackRate();
    this.watchChange();
  }

  /**
   * 监听属性变化
   */
  private watchChange() {
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
    watch(this.config, 'effect', (val) => {
      if (this.material) {
        const { uniforms, fragment } = effects(val)(this);
        this.material.uniforms = {
          ...this.material.uniforms,
          ...uniforms,
        };
        this.material.fragmentShader = Shader.GetEffectFragment({
          effect: fragment(),
        });
        this.material.uniformsNeedUpdate = true;
        this.material.needsUpdate = true;
      }
    });
    watch(this.config, 'positionInRender', (val) => {
      if (this.material) {
        const { width: currentWidth, height: currentHeight, left, bottom } = this.config.positionInRender;
        const { player, config } = this;
        const [uRange, vRange] = calcUVMap(
          val.fitType,
          player.width,
          player.height,
          currentWidth,
          currentHeight,
          config.originalWidth,
          config.originalHeight,
          new THREE.Vector2(left, bottom),
        );
        // @ts-ignore
        this.material.uniforms.uRange.value = uRange;
        // @ts-ignore
        this.material.uniforms.vRange.value = vRange;
        this.material.uniformsNeedUpdate = true;
      }
    });
  }

  private createTexture(): Promise<THREE.VideoTexture> {
    return new Promise(async (resolve, reject) => {
      this.video = document.createElement('video');
      this.video.loop = false;
      this.video.autoplay = false;
      this.video.addEventListener('loadedmetadata', async () => {
        if (this.video) {
          const texture = new THREE.VideoTexture(this.video);
          // @ts-ignore
          await texture.setCurrentTime(this.config.playRange.start);
          resolve(texture);
        }
      });
      this.video.src = this.config.blobUrl;
    });
  }

  private async initRender() {
    const {
      effect,
      positionInRender: { width: currentWidth, height: currentHeight, fitType, left, bottom },
      originalWidth,
      originalHeight,
    } = this.config;
    const { player } = this;
    const { uniforms, fragment } = effects(effect)(this);
    const [uRange, vRange] = calcUVMap(
      fitType,
      player.width,
      player.height,
      currentWidth,
      currentHeight,
      originalWidth,
      originalHeight,
      new THREE.Vector2(left, bottom),
    );
    this.texture = await this.createTexture();
    this.geometry = new THREE.PlaneGeometry(player.width, player.height);
    this.material = new THREE.ShaderMaterial({
      uniforms: {
        tex: { value: this.texture },
        uRange: { value: uRange },
        vRange: { value: vRange },
        ...uniforms,
      },
      vertexShader: Shader.GetVertex(),
      fragmentShader: Shader.GetEffectFragment({
        effect: fragment(),
      }),
    });
    this.material.uniformsNeedUpdate = true;
    this.material.transparent = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
  }

  public play() {
    this.video?.play();
    this.isPlaying = true;
  }

  public pause() {
    this.video?.pause();
    this.isPlaying = false;
  }

  public updatePlaybackRate() {
    const originalDuration = this.config.playRange.end - this.config.playRange.start;
    this.playbackRate = originalDuration / this.config.positionInTimeline.duration;
  }

  // 根据player的currentTime校正video的currentTime
  private async correctVideoCurrentTime() {
    const currentTime = this.player.currentTime;
    if (this.video && Math.abs(this.video.playbackRate - this.playbackRate) >= 10e-6) {
      this.video.playbackRate = this.playbackRate;
    }
    const newCurrentTime = this.config.playRange.start + (currentTime - this.config.positionInTimeline.start) * this.playbackRate;
    // @ts-ignore
    await this.texture.setCurrentTime(newCurrentTime);
  }

  public async onEnter() {
    // await this.correctVideoCurrentTime();
    if (this.player.isPlaying) {
      this.play();
    }
  }

  public async onLeave() {
    this.pause();
    if (this.texture) {
      // @ts-ignore
      await this.texture.setCurrentTime(this.config.playRange.start);
    }
  }

  public async onUpdate(isManual: boolean) {
    if (isManual) {
      await this.correctVideoCurrentTime();
    }
  }

  public async destroy() {
    super.destroy();
    if (this.video) {
      this.video = undefined;
    }
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}
