import { GifClip } from '../../common/types/clip';
import { ClipBase } from './base';
import * as THREE from 'three';
import { watch } from '../../common/utils/watch';
import effects from '../effects';
import { calcUVMap, Shader } from '../painter/shader';

export class Gif extends ClipBase<GifClip> {
  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.ShaderMaterial;
  private playbackRate = 1;
  private textureData?: Uint8ClampedArray;
  private currentFrame = 0;

  public texture?: THREE.DataTexture;

  public async init() {
    this.initRender();
    this.watchChange();
  }

  private watchChange() {
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
    watch(this.config, 'effect', (val) => {
      if (this.material) {
        const { uniforms, fragment } = effects(val)(this);
        this.material.uniforms = {
          ...this.material.uniforms,
          ...uniforms,
        };
        this.material.fragmentShader = Shader.GetEffectFragment({
          effect: fragment(),
        });
        this.material.uniformsNeedUpdate = true;
        this.material.needsUpdate = true;
      }
    });
    watch(this.config, 'positionInRender', (val) => {
      if (this.material) {
        const { width: currentWidth, height: currentHeight, left, bottom } = this.config.positionInRender;
        const { player, config } = this;
        const [uRange, vRange] = calcUVMap(
          val.fitType,
          player.width,
          player.height,
          currentWidth,
          currentHeight,
          config.originalWidth,
          config.originalHeight,
          new THREE.Vector2(left, bottom),
        );
        // @ts-ignore
        this.material.uniforms.uRange.value = uRange;
        // @ts-ignore
        this.material.uniforms.vRange.value = vRange;
        this.material.uniformsNeedUpdate = true;
      }
    });
  }

  private createTexture(data: Uint8ClampedArray, width: number, height: number): THREE.DataTexture {
    this.textureData = new Uint8ClampedArray(data);
    const texture = new THREE.DataTexture(this.textureData, width, height, THREE.RGBAFormat);
    texture.flipY = true;
    return texture;
  }

  private initRender() {
    const {
      positionInRender: {
        fitType,
        width: currentWidth,
        height: currentHeight,
        left,
        bottom,
      },
      originalWidth,
      originalHeight,
      effect,
      frames,
    } = this.config;
    const { player } = this;
    const { uniforms, fragment } = effects(effect)(this);
    const [uRange, vRange] = calcUVMap(
      fitType,
      player.width,
      player.height,
      currentWidth,
      currentHeight,
      originalWidth,
      originalHeight,
      new THREE.Vector2(left, bottom),
    );
    this.texture = this.createTexture(frames[0].patch, originalWidth, originalHeight);
    this.geometry = new THREE.PlaneGeometry(player.width, player.height);
    this.material = new THREE.ShaderMaterial({
      uniforms: {
        ...uniforms,
        tex: { value: this.texture },
        uRange: { value: uRange },
        vRange: { value: vRange },
      },
      vertexShader: Shader.GetVertex(),
      fragmentShader: Shader.GetEffectFragment({
        effect: fragment(),
      }),
    });
    this.material.transparent = true;
    this.material.uniformsNeedUpdate = true;
    this.sprite = new THREE.Mesh(this.geometry, this.material);
    this.sprite.position.z = this.config.zIndex;
  }

  private getFrame() {
    const currentTime = this.config.playRange.start + (this.player.currentTime - this.config.positionInTimeline.start) * this.playbackRate;
    let sumFrameDelay = 0;
    for (let i = 0; i < this.config.frames.length; i++) {
      const frame = this.config.frames[i];
      sumFrameDelay += frame.delay;
      if (currentTime <= sumFrameDelay && this.currentFrame !== i) {
        this.currentFrame = i;
        return frame;
      }
    }
  }

  public updatePlaybackRate() {
    const originalDuration = this.config.playRange.end - this.config.playRange.start;
    this.playbackRate = originalDuration / this.config.positionInTimeline.duration;
  }

  public async onUpdate() {
    const frame = this.getFrame();
    if (frame && this.texture && this.textureData) {
      this.textureData.set(frame.patch);
      this.texture.needsUpdate = true;
    }
  }

  public async destroy() {
    super.destroy();
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}
