import { Popover } from 'antd';
import React, { useCallback } from 'react';
import { getPlayerContainerBounding } from '../../../editor/util';
import IconFitType from '../../../icons/IconFitType';
import { getClipRenderInfo } from '../../../player/util';
import { Radius10Button } from '../../styleds';
import { Clip, ClipRenderInfo } from '../../types/clip';
import { FitType } from '../../types/player';
import FlatSelector from '../FlatSelector';

type Props = {
  clip: Clip;
  onChange(data: ClipRenderInfo): void;
};

const fitData = [
  { value: FitType.CUSTOM, label: '自定义' },
  { value: FitType.CONTAIN, label: '包含' },
  { value: FitType.COVER, label: '覆盖' },
  { value: FitType.FILL, label: '填充' },
];

const FitSelector: React.FC<Props> = ({
  clip,
  onChange,
}) => {
  const handleChange = useCallback((fitType: FitType) => {
    clip.positionInRender.fitType = fitType;
    const { height: playerHeight, width: playerWidth } = getPlayerContainerBounding();
    const renderInfo = getClipRenderInfo(clip, playerWidth, playerHeight);
    onChange({
      ...clip.positionInRender,
      ...renderInfo,
    });
  }, [clip, onChange]);
  return (
    <div>
      <Popover
        trigger="click"
        color="#1F2836"
        overlayStyle={{ width: 150 }}
        zIndex={999999999}
        content={
          <FlatSelector data={fitData} value={clip.positionInRender.fitType} onChange={handleChange} />
        }
      >
        <Radius10Button>
          <IconFitType />
        </Radius10Button>
      </Popover>
    </div>
  );
};

export default FitSelector;
