import { EffectType, FitType, TransitionType } from './player';
import { OverlayProps } from '../../player/svgs';
import { ParsedFrame } from 'gifuct-js';
import { ObjectType } from '.';

export enum ClipType {
  UNKNOWN,
  VIDEO,
  IMAGE,
  AUDIO,
  TRANSITION,
  SVG,
  LOTTIE,
  TEXT,
  GIF,
}

export interface ClipRenderInfo {
  fitType: FitType;
  rotate: number;
  left: number;
  bottom: number;
  width: number;
  height: number;
}

export interface Clip {
  id?: string;
  name: string;
  type: ClipType;
  /** 在时间轴上的位置 */
  positionInTimeline: {
    start: number;
    end: number;
    duration: number;
  };
  /** 在渲染界面上的位置 */
  positionInRender: ClipRenderInfo,
  /** 原始素材的播放范围 */
  playRange: {
    start: number;
    end: number;
  },
  effect: EffectType;
  originalWidth: number;
  originalHeight: number;
  zIndex: number;
}

export interface VideoClip extends Clip {
  url: string;
  blobUrl: string;
  buffer: ArrayBuffer;
}

export interface ImageClip extends Clip {
  url: string;
  blobUrl: string;
}

export interface AudioClip extends Clip {
  url: string;
  buffer: ArrayBuffer;
}

export interface LottieClip extends Clip {
  url: string;
  animationData: ObjectType;
}

export interface TextClip extends Clip {
  value: string;
  props: {
    fontColor: string;
    fontSize: number;
    fontFamily: string;
  };
}

export interface SvgClip extends Clip {
  func(props: OverlayProps): string; 
}

export interface GifClip extends Clip {
  url: string;
  frames: ParsedFrame[];
}

export interface TransitionClip extends Clip {
  transition: TransitionType;
  fromId?: string;
  toId?: string;
}

export interface AudioMergeItem {
  /** 在整个player中的开始时间 */
  delay: number;
  /** 原音频中的开始时间 */
  from: number;
  /** 原音频中的结束时间 */
  to: number;
  /** 播放速度 */
  tempo: number;
  type: ClipType;
  buffer: ArrayBuffer;
}
