import React, { useCallback, useState } from 'react';
// import MenuAudio from '../../../icons/MenuAudio';
// import MenuImage from '../../../icons/MenuImage';
import MenuMedia from '../../../icons/MenuMedia';
import MenuMine from '../../../icons/MenuMine';
// import MenuShape from '../../../icons/MenuShape';
import MenuText from '../../../icons/MenuText';
import MenuTransition from '../../../icons/MenuTransition';
// import MenuVideo from '../../../icons/MenuVideo';
// import AudioPanel from './components/AudioPanel';
// import ImagePanel from './components/ImagePanel';
import MediaPanel from './components/MediaPanel';
import MenuItem from './components/MenuItem';
import { ResourcePanel } from './components/MenuItem/styled';
import MinePanel from './components/MinePanel';
// import ShapePanel from './components/ShapePanel';
import TextPanel from './components/TextPanel';
import TransitionPanel from './components/TransitionPanel';
// import VideoPanel from './components/VideoPanel';
import { StyledAssetMenu } from './styled';

const menuList = [
  { title: '我的素材', icon: <MenuMine />, resourcePanel: <MinePanel /> },
  { title: '转场', icon: <MenuTransition />, resourcePanel: <TransitionPanel /> },
  { title: '文字', icon: <MenuText />, resourcePanel: <TextPanel /> },
  // { title: '图片', icon: <MenuImage />, resourcePanel: <ImagePanel /> },
  // { title: '声音', icon: <MenuAudio />, resourcePanel: <AudioPanel /> },
  // { title: '视频', icon: <MenuVideo />, resourcePanel: <VideoPanel /> },
  // { title: '形状', icon: <MenuShape />, resourcePanel: <ShapePanel /> },
  { title: '多媒体', icon: <MenuMedia />, resourcePanel: <MediaPanel /> },
];

const AssetMenu: React.FC = () => {
  const [activeTab, setActiveTab] = useState<string>('多媒体');
  const handleMenuItemClick = useCallback((title: string) => {
    if (activeTab !== title) {
      setActiveTab(title);
    }
  }, [activeTab]);
  return (
    <StyledAssetMenu>
      {
        menuList.map((item) => {
          return (
            <MenuItem
              key={item.title}
              title={item.title}
              icon={item.icon}
              resourcePanel={item.resourcePanel}
              isActive={activeTab === item.title}
              onClick={handleMenuItemClick}
            />
          );
        })
      }
      {
        menuList.map((item) => {
          return (
            <ResourcePanel
              key={item.title}
              style={{ ...(!(activeTab === item.title) ? { display: 'none' } : null) }}
            >
              {item.resourcePanel}
            </ResourcePanel>
          );
        })
      }
    </StyledAssetMenu>
  );
};

export default AssetMenu;
