import { calcUVMap, Shader } from '../painter/shader';
import effects from '../effects/index';
import transitions from '../transitions';
import { ClipBase } from './base';
import { TransitionClip } from '../../common/types/clip';
import * as THREE from 'three';
import { watch } from '../../common/utils/watch';

export class Transition extends ClipBase<TransitionClip> {
  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.ShaderMaterial;
  private fromClip?: ClipBase;
  private toClip?: ClipBase;

  public async init() {
    await this.initRender();
    this.watchChange();
  }

  private watchChange() {
    if (this.fromClip && this.toClip) {
      watch(this.fromClip.config, 'effect', () => {
        this.updateShader();
      });
      watch(this.fromClip.config, 'positionInRender', () => {
        this.updateUniforms();
      });
      watch(this.toClip.config, 'effect', () => {
        this.updateShader();
      });
      watch(this.toClip.config, 'positionInRender', () => {
        this.updateUniforms();
      });
    }
  }

  private updateUniforms() {
    if (this.fromClip && this.toClip && this.material) {
      const {
        originalWidth: fromOriginalWidth,
        originalHeight: fromOriginalHeight,
        positionInRender: { width: fromCurrentWidth, height: fromCurrentHeight, fitType: fromFitType, left: fromLeft, bottom: fromBottom },
      } = this.fromClip.config;
      const {
        originalWidth: toOriginalWidth, 
        originalHeight: toOriginalHeight,
        positionInRender: { width: toCurrentWidth, height: toCurrentHeight, fitType: toFitType, left: toLeft, bottom: toBottom },
      } = this.toClip.config;
      const { player } = this;
      const [fromURange, fromVRange] = calcUVMap(
        fromFitType,
        player.width,
        player.height,
        fromCurrentWidth,
        fromCurrentHeight,
        fromOriginalWidth,
        fromOriginalHeight,
        new THREE.Vector2(fromLeft, fromBottom),
      );
      const [toURange, toVRange] = calcUVMap(
        toFitType,
        player.width,
        player.height,
        toCurrentWidth,
        toCurrentHeight,
        toOriginalWidth,
        toOriginalHeight,
        new THREE.Vector2(toLeft, toBottom),
      );
      // @ts-ignore
      this.material.uniforms.fromURange.value = fromURange;
      // @ts-ignore
      this.material.uniforms.fromVRange.value = fromVRange;
      // @ts-ignore
      this.material.uniforms.toURange.value = toURange;
      // @ts-ignore
      this.material.uniforms.toVRange.value = toVRange;
      this.material.uniformsNeedUpdate = true;
    }
  }

  private updateShader() {
    if (this.fromClip && this.toClip && this.material) {
      const { transition } = this.config;
      const { effect: fromEffect } = this.fromClip.config;
      const { effect: toEffect } = this.toClip.config;
      const { uniforms: fromUniforms, fragment: fromFragment } = effects(fromEffect)();
      const { uniforms: toUniforms, fragment: toFragment } = effects(toEffect)();
      const { uniforms: transitionUniforms, fragment: transitionFragment } = transitions(transition);
      this.material.uniforms = {
        ...this.material.uniforms,
        ...fromUniforms,
        ...toUniforms,
        ...transitionUniforms,
      };
      this.material.fragmentShader = Shader.GetTransitionFragment({
        fromEffect: fromFragment('from', 'fromEffect'),
        toEffect: toFragment('to', 'toEffect'),
        transition: transitionFragment,
      });
      this.material.needsUpdate = true;
    }
  }

  // 由于不知道关联视频或者图片的初始状态，所以需要等到渲染的时候再初始化
  private async initRender() {
    const { player } = this;
    const { fromId, toId, transition } = this.config;
    const fromClip = this.fromClip = player.getClipById(fromId);
    const toClip = this.toClip = player.getClipById(toId);
    if (fromClip && toClip) {
      const {
        originalWidth: fromOriginalWidth,
        originalHeight: fromOriginalHeight,
        positionInRender: { width: fromCurrentWidth, height: fromCurrentHeight, fitType: fromFitType },
        effect: fromEffect,
      } = fromClip.config;
      const {
        originalWidth: toOriginalWidth, 
        originalHeight: toOriginalHeight,
        positionInRender: { width: toCurrentWidth, height: toCurrentHeight, fitType: toFitType },
        effect: toEffect,
      } = toClip.config;
      const { uniforms: fromUniforms, fragment: fromFragment } = effects(fromEffect)();
      const { uniforms: toUniforms, fragment: toFragment } = effects(toEffect)();
      const { uniforms: transitionUniforms, fragment: transitionFragment } = transitions(transition);
      const [fromURange, fromVRange] = calcUVMap(
        fromFitType,
        player.width,
        player.height,
        fromCurrentWidth,
        fromCurrentHeight,
        fromOriginalWidth,
        fromOriginalHeight,
      );
      const [toURange, toVRange] = calcUVMap(
        toFitType,
        player.width,
        player.height,
        toCurrentWidth,
        toCurrentHeight,
        toOriginalWidth,
        toOriginalHeight,
      );
      this.geometry = new THREE.PlaneGeometry(player.width, player.height);
      this.material = new THREE.ShaderMaterial({
        uniforms: {
          from: { value: fromClip.texture },
          to: { value: toClip.texture },
          progress: { value: 0 },
          fromURange: { value: fromURange },
          fromVRange: { value: fromVRange },
          toURange: { value: toURange },
          toVRange: { value: toVRange },
          ...fromUniforms,
          ...toUniforms,
          ...transitionUniforms,
        },
        vertexShader: Shader.GetVertex(),
        fragmentShader: Shader.GetTransitionFragment({
          fromEffect: fromFragment('from', 'fromEffect'),
          toEffect: toFragment('to', 'toEffect'),
          transition: transitionFragment,
        }),
      });
      this.material.uniformsNeedUpdate = true;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
      this.sprite.position.z = toClip.config.zIndex;
    }
  }

  public async onEnter() {
    if (this.fromClip && this.toClip) {
      this.fromClip.hide();
      this.toClip.hide();
      this.render();
    }
  }

  public async onLeave() {
    if (this.fromClip && this.toClip) {
      this.fromClip.show();
      this.toClip.show();
    }
  }

  public async onUpdate() {
    this.render();
  }

  private async render() {
    if (this.material?.uniforms) {
      const { start, end } = this.config.positionInTimeline;
      const progress = (this.player.currentTime - start) / (end - start);
      this.material.uniforms.progress.value = progress;
    }
  }

  public async destroy() {
    super.destroy();
    this.texture?.dispose();
    this.geometry?.dispose();
    this.material?.dispose();
  }
 }
