import { createFFmpeg, FFmpeg } from '@ffmpeg/ffmpeg';
import { v4 } from 'uuid';
import { AsyncAutoQueue } from '../common/libs/queue';
import { MediaInfo } from '../common/types';
import { AudioMergeItem } from '../common/types/clip';
import { extractAudioInfo, extractDuration, extractVideoInfo, getAudioTempoChain } from './util';

class FFmpegHelperClass {
  public static ffmpeg: FFmpeg;

  public static async Load() {
    if (!FFmpegHelper.ffmpeg.isLoaded()) {
      await FFmpegHelper.ffmpeg.load();
    }
  }

  public static async GetVersion() {
    await FFmpegHelper.ffmpeg.run('-version');
  }

  public static async VideoTranscode(buffer: ArrayBuffer, codec = 'h264') {
    const ffmpeg = FFmpegHelperClass.ffmpeg;
    const inputFileName = `${v4()}`;
    const outputFileName = `${v4()}.mp4`;
    // 写入文件
    ffmpeg.FS('writeFile', inputFileName, new Uint8Array(buffer));
    // 执行命令
    await ffmpeg.run('-i', inputFileName, '-c:v', codec, '-c:a', 'aac', outputFileName);
    // 读取生成的文件
    const video = ffmpeg.FS('readFile', outputFileName);
    // 删除写入的文件
    ffmpeg.FS('unlink', inputFileName);
    ffmpeg.FS('unlink', outputFileName);
    return video.buffer;
  }

  public static async GetMediaInfo(buffer: ArrayBuffer) {
    const ffmpeg = FFmpegHelperClass.ffmpeg;
    const inputFileName = `${v4()}`;
    const mediaInfo: MediaInfo = {};
    ffmpeg.setLogger(({ type, message }) => {
      if (type === 'fferr') {
        if (message.includes('Duration')) {
          mediaInfo.duration = extractDuration(message);
        } else if (message.includes('Stream #0')) { // 视频流或者音频流
          if (message.includes('Video:')) {
            mediaInfo.video = extractVideoInfo(message);
          } else if (message.includes('Audio:')) {
            mediaInfo.audio = extractAudioInfo(message);
          }
        }
      }
    });
    // 写入文件
    ffmpeg.FS('writeFile', inputFileName, new Uint8Array(buffer));
    // 执行命令
    await ffmpeg.run('-i', inputFileName);
    // 删除写入的文件
    ffmpeg.FS('unlink', inputFileName);
    return mediaInfo;
  }

  public static async MergeAudios(items: AudioMergeItem[]) {
    const ffmpeg = FFmpegHelperClass.ffmpeg;
    const outputFileName = `${v4()}.aac`;
    const inputFileName = v4();
    const inputs = items.map((item, index) => {
      return ['-i', `${inputFileName}_${index}`, '-ss', `${item.from / 1000}`, '-to', `${(item.to - item.from) / 1000}`];
    }).flat(1);
    const filters = items.map((item, index) => {
      const delay = item.delay / 1000;
      return `[${index}:a]adelay=${delay}|${delay}|${delay}|${delay}${getAudioTempoChain(item.tempo)}[a${index}];`;
    }).join('');
    const amixPrefix = items.map((item, index) => {
      return `[a${index}]`;
    }).join('');
    // 写入文件
    items.forEach((item, index) => {
      ffmpeg.FS('writeFile', `${inputFileName}_${index}`, new Uint8Array(item.buffer));
    });
    // 执行命令
    await ffmpeg.run(
      ...inputs,
      '-filter_complex',
      `${filters}${amixPrefix}amix=inputs=${items.length}[aout]`,
      '-map', '[aout]',
      outputFileName
    );
    // 读取生成的文件
    const audio = ffmpeg.FS('readFile', outputFileName);
    // 删除写入的文件
    items.forEach((item, index) => {
      ffmpeg.FS('unlink', `${inputFileName}_${index}`);
    });
    ffmpeg.FS('unlink', outputFileName);
    return audio.buffer;
  }
  
  public static async ImagesToVideo(frames: ArrayBuffer[], fps: number = 30) {
    const ffmpeg = FFmpegHelperClass.ffmpeg;
    const outputFileName = `${v4()}.mp4`;
    // 写入文件
    frames.forEach((item, index) => {
      ffmpeg.FS('writeFile', `export_${index.toString().padStart(4, '0')}.jpg`, new Uint8Array(item));
    });
    // 执行命令
    await ffmpeg.run(
      '-framerate', `${fps}`,
      '-i', `export_%04d.jpg`,
      '-c:v', 'libx264',
      '-pix_fmt', 'yuv420p',
      '-preset', 'ultrafast',
      '-crf', '1',
      outputFileName
    );
    // 读取生成的文件
    const video = ffmpeg.FS('readFile', outputFileName);
    // 删除写入的文件
    frames.forEach((item, index) => {
      ffmpeg.FS('unlink', `export_${index.toString().padStart(4, '0')}.jpg`);
    });
    ffmpeg.FS('unlink', outputFileName);
    return video.buffer;
  }
  
  public static async MergeVideoAudio(videoBuffer: ArrayBufferLike, audioBuffer: ArrayBufferLike) {
    const ffmpeg = FFmpegHelperClass.ffmpeg;
    const videoFileName = `${v4()}.mp4`;
    const audioFileName = `${v4()}.aac`;
    const outputFileName = `${v4()}.mp4`;
    // 写入文件
    ffmpeg.FS('writeFile', videoFileName, new Uint8Array(videoBuffer));
    ffmpeg.FS('writeFile', audioFileName, new Uint8Array(audioBuffer));
    // 执行命令
    await ffmpeg.run(
      '-i', videoFileName,
      '-i', audioFileName,
      '-c', 'copy',
      outputFileName
    );
    // 读取生成的文件
    const finalVideo = ffmpeg.FS('readFile', outputFileName);
    // 删除写入的文件
    ffmpeg.FS('unlink', videoFileName);
    ffmpeg.FS('unlink', audioFileName);
    ffmpeg.FS('unlink', outputFileName);
    return finalVideo.buffer;
  }
}

FFmpegHelperClass.ffmpeg = createFFmpeg({ log: true });

const asyncQueue = new AsyncAutoQueue();

const FFmpegHelper = new Proxy(FFmpegHelperClass, {
  get(target, prop: keyof FFmpegHelperClass) {
    const result = prop in target ? target[prop] : undefined;
    if (typeof result === 'function') {
      return async function(...args: any[]) {
        return asyncQueue.enqueue(async () => {
          await FFmpegHelperClass.Load();
          return await (result as unknown as Function).apply(target, args);
        });
      }
    }
    return result;
  }
});

export default FFmpegHelper;
