import React, { useEffect } from 'react';
import { LottieClip } from '../../../../../common/types/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { ObjectType } from '../../../../../common/types';
import { getLottieInfo } from '../../../../../common/utils';

type Props = {
  data: LottieClip;
  onLoaded(data: LottieClip): void;
};

const LottieCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data?.originalWidth) {
      fetch(data.url).then((response) => {
        return response.json();
      }).then(async (animationData: ObjectType) => {
        const { width, height, duration } = await getLottieInfo(animationData);
        onLoaded({
          ...data,
          positionInTimeline: {
            ...data.positionInTimeline,
            duration,
          },
          positionInRender: {
            ...data.positionInRender,
            width,
            height,
          },
          playRange: {
            start: 0,
            end: duration,
          },
          originalWidth: width,
          originalHeight: height,
          animationData,
        });
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default LottieCard;
