import { Popover } from 'antd';
import React from 'react';
import IconEffect from '../../../icons/IconEffect';
import { Radius10Button } from '../../styleds';
import { EffectType } from '../../types/player';
import FlatSelector from '../FlatSelector';

type Props = {
  value: EffectType;
  onChange(data: EffectType): void;
};

const effectData = [
  { value: EffectType.NO_EFFECT, label: '无特效' },
  { value: EffectType.GRAY, label: '灰度' },
  { value: EffectType.BLUR, label: '高斯模糊' },
];

const EffectSelector: React.FC<Props> = ({
  value,
  onChange,
}) => {
  return (
    <div>
      <Popover
        trigger="click"
        color="#1F2836"
        overlayStyle={{ width: 150 }}
        zIndex={999999999}
        content={
          <FlatSelector data={effectData} value={value} onChange={onChange} />
        }
      >
        <Radius10Button>
          <IconEffect />
        </Radius10Button>
      </Popover>
    </div>
  );
};

export default EffectSelector;
