import { useSize } from 'ahooks';
import React, { useCallback, useContext, useEffect, useRef, useState } from 'react';
import Moveable from 'react-moveable';
import { editorContext } from '../..';
import { ClipType, TextClip } from '../../../common/types/clip';
import { FitType } from '../../../common/types/player';
import { getPlayerContainerBounding } from '../../util';
import TextEditor from './components/TextEditor';
import { StyledTarget } from './styled';

const renderDirections = ['nw', 'ne', 'sw', 'se'];

const ResizeRotate: React.FC = () => {
  const { activeClip, tracks, setTracks } = useContext(editorContext);
  const [target, setTarget] = useState<HTMLDivElement | null>(null);
  const [targetStyle, setTargetStyle] = useState<React.CSSProperties>();
  const moveableRef = useRef<Moveable>(null);
  const targetRef = useCallback((node: HTMLDivElement) => {
    setTarget(node);
  }, []);

  useEffect(() => {
    if (activeClip) {
      const { left: playerLeft, top, height: playerHeight } = getPlayerContainerBounding();
      const { width, height, left, bottom } = activeClip.positionInRender;
      setTargetStyle({
        width,
        height,
        transform: `translate(${playerLeft + left}px, ${top + playerHeight - bottom - height}px)`,
      });
    } else {
      setTargetStyle({
        width: 0,
        height: 0,
      });
    }
  }, [activeClip, activeClip?.positionInRender]);

  const { width, height } = useSize(target);
  useEffect(() => {
    moveableRef.current?.updateTarget();
  }, [width, height]);

  const handleStart = useCallback(() => {
    if (activeClip && activeClip.positionInRender.fitType !== FitType.CUSTOM) {
      Object.assign(activeClip, {
        positionInRender: {
          ...activeClip.positionInRender,
          fitType: FitType.CUSTOM,
        },
      });
      setTracks([...tracks]);
    }
  }, [activeClip, setTracks, tracks]);

  const handleDrag = useCallback(({ target, delta, transform }) => {
    target.style.transform = transform;
    if (activeClip) {
      Object.assign(activeClip, {
        positionInRender: {
          ...activeClip.positionInRender,
          left: activeClip.positionInRender.left + delta[0],
          bottom: activeClip.positionInRender.bottom - delta[1],
        },
      });
    }
  }, [activeClip]);

  const handleResize = useCallback(({ target, width, height, delta, drag, direction }) => {
    const isDragBottom = direction[1] === 1;
    const isDragLeft = direction[0] === -1;
    const beforeTranslate = drag.beforeTranslate;
    if (activeClip) {
      Object.assign(activeClip, {
        positionInRender: {
          ...activeClip.positionInRender,
          fitType: FitType.CUSTOM,
          width: width,
          height: height,
          ...(isDragLeft ? { left: activeClip.positionInRender.left - delta[0] } : null),
          ...(isDragBottom ? { bottom: activeClip.positionInRender.bottom - delta[1] } : null),
        },
      });
    }
    delta[0] && (target!.style.width = `${width}px`);
    delta[1] && (target!.style.height = `${height}px`);
    target!.style.transform = `translate(${beforeTranslate[0]}px, ${beforeTranslate[1]}px)`;
  }, [activeClip]);

  return (
    <>
      <StyledTarget className="resize-rotate" style={targetStyle} ref={targetRef}>
        {
          activeClip?.type === ClipType.TEXT ? <TextEditor clip={activeClip as TextClip} /> : null
        }
      </StyledTarget>
      <Moveable
        ref={moveableRef}
        target={target}
        container={null}
        origin={false}
        keepRatio={true}
        renderDirections={renderDirections}

        /* Resize event edges */
        edge={false}

        /* draggable */
        draggable={true}
        throttleDrag={2}
        onDragStart={handleStart}
        onDrag={handleDrag}

        /* resizable*/
        resizable={true}
        throttleResize={2}
        onResizeStart={handleStart}
        onResize={handleResize}
      />
    </>
  );
};

export default ResizeRotate;
