import React, { useEffect, useRef } from 'react';
import { VideoClip } from '../../../../../../../common/types/clip';
import { TRACK_HEIGHT } from '../../../../../../config';
import { millisecondsToWidth } from '../../../../../../util';

type Props = {
  clip: VideoClip;
};

function seekVideo(video: HTMLVideoElement, currentTime: number) {
  return new Promise((resolve) => {
    function seeked() {
      resolve(true);
    }
    // @ts-ignore
    video.requestVideoFrameCallback(seeked);
    video.currentTime = currentTime / 1000;
  });
}

async function drawVideoToCanvas(
  canvas: HTMLCanvasElement,
  videoUrl: string,
  snapWidth: number,
  snapHeight: number,
  snapCount: number,
  from: number,
  to: number,
) {
  const timeStep = (to - from) / snapCount;
  const ctx = canvas.getContext('2d');
  const video = document.createElement('video');
  video.src = videoUrl;
  let index = 0;
  while (index < snapCount) {
    await seekVideo(video, from + timeStep * index + 1);
    ctx?.drawImage(video, snapWidth * index, 0, snapWidth, snapHeight);
    index += 1;
  }
}

const VideoTrack: React.FC<Props> = ({ clip }) => {
  const { positionInTimeline, originalWidth, originalHeight, playRange, blobUrl } = clip;
  const canvasRef = useRef<HTMLCanvasElement>(null);
  useEffect(() => {
    const canvas = canvasRef.current;
    if (canvas) {
      const { width, height: snapHeight } = canvas.getBoundingClientRect();
      const snapWidth = snapHeight * originalWidth / originalHeight;
      const snapCount = Math.ceil(width / snapWidth); // 需要展示多少snap
      drawVideoToCanvas(canvas, blobUrl, snapWidth, snapHeight, snapCount, playRange.start, playRange.end);
    }
  }, [blobUrl, playRange.end, playRange.start, originalHeight, originalWidth, positionInTimeline.duration]);
  return (
    <div style={{ width: '100%' }}>
      <div style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
        <canvas
          ref={canvasRef}
          width={millisecondsToWidth(positionInTimeline.duration)}
          height={TRACK_HEIGHT}
        />
      </div>
    </div>
  );
};

export default VideoTrack;
