import { Popover } from 'antd';
import React from 'react';
import { Ratio } from '../../configs';
import { Radius99Button } from '../../styleds';
import FlatSelector from '../FlatSelector';

type Props = {
  value: Ratio;
  onChange(data: Ratio): void;
};

const ratioData = [
  { value: Ratio.RATIO_16_9, label: '16:9' },
  { value: Ratio.RATIO_9_16, label: '9:16' },
  { value: Ratio.RATIO_4_3, label: '4:3' },
  { value: Ratio.RATIO_3_4, label: '3:4' },
  { value: Ratio.RATIO_21_9, label: '21:9' },
  { value: Ratio.RATIO_1_1, label: '1:1' },
];

const RatioSelector: React.FC<Props> = ({
  value,
  onChange,
}) => {
  return (
    <div>
      <Popover
        trigger="click"
        color="#1F2836"
        overlayStyle={{ width: 150 }}
        zIndex={999999999}
        content={
          <FlatSelector data={ratioData} value={value} onChange={onChange} />
        }
      >
        <Radius99Button>
          {
            ratioData.find(item => item.value === value)?.label
          }
        </Radius99Button>
      </Popover>
    </div>
  );
};

export default RatioSelector;
