import React, { useEffect } from 'react';
import { StyledAssetCard } from '../../../../../common/styleds';
import { VideoClip } from '../../../../../common/types/clip';

type Props = {
  data: VideoClip;
  onLoaded(data: VideoClip): void;
};

const VideoCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data.blobUrl) {
      fetch(data.url).then((response) => {
        return response.blob();
      }).then(async (file: Blob) => {
        const buffer = await file.arrayBuffer();
        const blobUrl = URL.createObjectURL(file);
        const video = document.createElement('video');
        video.loop = false;
        video.addEventListener('loadedmetadata', () => {
          const duration = Math.round(video.duration * 1000);
          onLoaded({
            ...data,
            positionInTimeline: {
              ...data.positionInTimeline,
              duration: duration,
            },
            positionInRender: {
              ...data.positionInRender,
              width: video.videoWidth,
              height: video.videoHeight,
            },
            playRange: {
              start: 0,
              end: duration,
            },
            originalWidth: video.videoWidth,
            originalHeight: video.videoHeight,
            blobUrl,
            buffer,
          });
        });
        video.src = blobUrl;
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <video src={data.blobUrl} />
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default VideoCard;
