import React, { useCallback, useRef } from 'react';
import ContentEditable, { ContentEditableEvent } from 'react-contenteditable';
import { TextClip } from '../../../../../common/types/clip';
import { getTextClipSize } from '../../../../../common/utils';

type Props = {
  clip: TextClip;
};

const TextEditor: React.FC<Props> = ({ clip }) => {
  const { width, height } = clip.positionInRender;
  const text = useRef(clip.value);
  const contentEditableRef = useRef<HTMLElement>(null);
  const handleChange = useCallback((e: ContentEditableEvent) => {
    text.current = e.target.value;
    Object.assign(clip, { value: text.current });
    const newSize = getTextClipSize(clip);
    Object.assign(clip, {
      positionInRender: {
        ...clip.positionInRender,
        width: newSize.width,
        height: newSize.height,
      },
    });
  }, [clip]);

  const handleBlur = useCallback(() => {
    console.log(text.current);
  }, []);
  return (
    <div style={{ width: width, height: height, overflow: 'hidden' }}>
      <ContentEditable
        innerRef={contentEditableRef}
        html={text.current} // innerHTML of the editable div
        disabled={false} // use true to disable editing
        onChange={handleChange} // handle innerHTML change
        onBlur={handleBlur}
      />
    </div>
  );
};

export default TextEditor;
