import { ClipBase } from '../clips/base';
import * as THREE from 'three';
import { glsl } from 'typed-glsl';

export function blur(clip?: ClipBase) {
  return {
    uniforms: {
      blurSize: { value: 10 },
      horizontalPass: { value: 1 }, // 0 or 1 to indicate vertical or horizontal pass
      /*
        The sigma value for the gaussian function: higher value means more blur
        A good value for 9x9 is around 3 to 5
        A good value for 7x7 is around 2.5 to 4
        A good value for 5x5 is around 2 to 3.5
      */
      sigma: { value: 3.0 },
      texOffset: { value: new THREE.Vector2(1.0, 1.0) },
    },
    fragment: (texName = 'tex', funName = 'effect') => glsl`
      const float PI = 3.14159265;
      vec4 ${funName}(vec2 uv) {
        float numBlurPixelsPerSide = float(blurSize / 2);

        vec3 incrementalGaussian;
        incrementalGaussian.x = 1.0 / (sqrt(2.0 * PI) * sigma);
        incrementalGaussian.y = exp(-0.5 / (sigma * sigma));
        incrementalGaussian.z = incrementalGaussian.y * incrementalGaussian.y;

        vec4 avgValue = vec4(0.0, 0.0, 0.0, 0.0);
        float coefficientSum = 0.0;
        avgValue += texture2D(${texName}, uv) * incrementalGaussian.x;
        coefficientSum += incrementalGaussian.x;
        incrementalGaussian.xy *= incrementalGaussian.yz;

        for (float i = 1.0; i <= numBlurPixelsPerSide; i++) { 
          avgValue += texture2D(${texName}, uv - i * texOffset) * incrementalGaussian.x;         
          avgValue += texture2D(${texName}, uv + i * texOffset) * incrementalGaussian.x;         
          coefficientSum += 2.0 * incrementalGaussian.x;
          incrementalGaussian.xy *= incrementalGaussian.yz;
        }

        return avgValue / coefficientSum;
      }
    `
  };
}

