import React, { useCallback } from 'react';
import { MenuItemContainer } from '../../../../../common/styleds';

type Props = {
  isActive: boolean;
  title: string;
  icon: React.ReactNode;
  resourcePanel?: React.ReactNode;
  onClick(title: string): void;
};

const MenuItem: React.FC<Props> = ({ icon, title, isActive, onClick }) => {
  const handleClick = useCallback(() => {
    onClick(title);
  }, [onClick, title]);
  return (
    <>
      <MenuItemContainer className={isActive ? 'active' : ''} onClick={handleClick}>
        <div className="icon">{icon}</div>
        <div className="title">{title}</div>
      </MenuItemContainer>
    </>
  );
};

export default MenuItem;
