import React, { useEffect } from 'react';
import { StyledAssetCard } from '../../../../../common/styleds';
import { AudioClip } from '../../../../../common/types/clip';

type Props = {
  data: AudioClip;
  onLoaded(data: AudioClip): void;
};

const AudioCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data.buffer) {
      fetch(data.url).then((response) => {
        return response.blob();
      }).then((file: Blob) => {
        const blobUrl = URL.createObjectURL(file);
        const audio = document.createElement('audio');
        audio.loop = false;
        audio.addEventListener('loadedmetadata', () => {
          file.arrayBuffer().then((buffer) => {
            const duration = Math.round(audio.duration * 1000);
            onLoaded({
              ...data,
              positionInTimeline: {
                ...data.positionInTimeline,
                duration: duration,
              },
              playRange: {
                start: 0,
                end: duration,
              },
              buffer,
            });
          }).catch((error) => {
            alert(error.message);
          });
        });
        audio.src = blobUrl;
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <audio src={data.url} />
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default AudioCard;
