import Painter from './painter';
import { Clip, ClipType } from '../common/types/clip';
import { ClipBase } from './clips/base';
import { createClip } from './util';
import { watch } from '../common/utils/watch';
import { Transition } from './clips/transition';

type Props = {
  width: number;
  height: number;
  onCurrentTimeChange?(currentTime: number): void;
  onEnd?(): void;
};

export default class Player {
  // 自动播放相关
  private tickTime = 0;
  private rafId = 0;
  private tickFunc;

  private onCurrentTimeChangeCallback?: (currentTime: number) => void;
  private onEnd?: () => void;

  public painter: Painter;
  public clips: ClipBase<Clip>[] = [];

  /**
   * 播放相关属性
   */
  public currentTime = 0;
  public duration = 0;
  public isPlaying = false;
  public width = 0;
  public height = 0;

  constructor({ width, height, onCurrentTimeChange, onEnd }: Props) {
    this.width = width;
    this.height = height;
    this.painter = new Painter(width, height);
    this.onCurrentTimeChangeCallback = onCurrentTimeChange;
    this.onEnd = onEnd;
    this.tickFunc = this.tick.bind(this);
    this.rafId = requestAnimationFrame(this.tickFunc);
  }

  private async tick(time: number) {
    await this.render(time);
    this.rafId = requestAnimationFrame(this.tickFunc);
  }

  public isInRange(clip: ClipBase<Clip>) {
    const { start, end } = clip.config.positionInTimeline;
    return start <= this.currentTime && end >= this.currentTime;
  }

  public updateResolution(width: number, height: number): boolean {
    if (width === this.width && height === this.height) {
      return false;
    }
    this.width = width;
    this.height = height;
    this.painter.updateSize(width, height);
    return true;
  }

  public updateDuration() {
    this.duration = 0;
    this.clips.forEach(({ config }) => {
      if (config.positionInTimeline.end > this.duration) {
        this.duration = config.positionInTimeline.end;
      }
    });
    if (this.duration === 0) {
      this.currentTime = 0;
    }
  }

  public getClipById(id?: string) {
    return this.clips.find((clip) => clip.config.id === id);
  }

  public async addClip(clip: Clip) {
    const newClip = createClip(clip, this) as ClipBase<Clip>;
    await newClip.init();
    this.clips.push(newClip);
    await this.onCurrentTimeChange();
    this.updateDuration();

    // 位置改变时
    watch(newClip.config, 'positionInTimeline', async (val) => {
      newClip.updatePlaybackRate();
      if (this.isInRange(newClip)) {
        if (newClip.isEntered) {
          await newClip.onUpdate(true);
        } else {
          newClip.isEntered = true;
          newClip.addToPainter();
          await newClip.onEnter(true);
        }
      } else {
        if (newClip.isEntered) {
          newClip.isEntered = false;
          newClip.removeFromPainter();
          await newClip.onLeave(true);
        }
      }
    });
    // watch(newClip.config, 'playRange', async () => {
    //   await newClip.onUpdate(true);
    // });
  }

  public async removeClip(id?: string) {
    const clip = this.getClipById(id);
    if (clip) {
      await clip.destroy();
      if (clip.config.type === ClipType.TRANSITION) {
        const fromClip = this.getClipById((clip as Transition).config.fromId);
        const toClip = this.getClipById((clip as Transition).config.toId);
        fromClip?.show();
        toClip?.show();
      }
      this.clips.splice(this.clips.indexOf(clip), 1);
      this.updateDuration();
    }
  }

  private async render(time: number) {
    const intTime = Math.floor(time); // 转为整数
    if (this.isPlaying) {
      this.currentTime += intTime - this.tickTime;
      this.currentTime = Math.min(this.currentTime, this.duration);
      await this.onCurrentTimeChange();
      if (this.currentTime >= this.duration) {
        this.pause();
        this.onEnd?.();
      }
    }
    this.painter.render();
    this.tickTime = intTime;
  }

  /**
   * 播放
   */
  public async play() {
    if (!this.isPlaying) {
      this.isPlaying = true;
      this.clips.forEach((clip) => {
        if (this.isInRange(clip)) {
          clip.play();
        }
      });
    }
  }

  /**
   * 暂停
   */
  public async pause() {
    if (this.isPlaying) {
      this.isPlaying = false;
      this.clips.forEach((clip) => {
        clip.pause();
      });
    }
  }

  /**
   * 手动更新时间戳
   */
  public async setCurrentTime(currentTime: number) {
    this.currentTime = Math.max(0, Math.min(currentTime, this.duration));
    await this.onCurrentTimeChange(true);
    if (this.currentTime >= this.duration) {
      this.pause();
      this.onEnd?.();
    }
  }

  /**
   * 时间戳变化
   */
  private async onCurrentTimeChange(isManual: boolean = false) {
    let task: Promise<void>[] = [];
    this.clips.forEach((clip) => {
      if (this.isInRange(clip)) {
        if (clip.isEntered) {
          task.push(clip.onUpdate(isManual));
        } else {
          clip.isEntered = true;
          clip.addToPainter();
          task.push(clip.onEnter(isManual));
        }
      } else {
        if (clip.isEntered) {
          clip.isEntered = false;
          clip.removeFromPainter();
          task.push(clip.onLeave(isManual));
        }
      }
    });
    await Promise.all(task).catch((error) => {
      alert(error.message);
    });
    task = [];
    this.onCurrentTimeChangeCallback?.(this.currentTime);
  }

  /**
   * 将播放器插入到指定节点
   */
  public attachTo(container: HTMLElement) {
    container?.appendChild(this.painter.getDomElement());
  }

  /**
   * 销毁播放器
   */
  public async destroy() {
    cancelAnimationFrame(this.rafId);
    let task: Promise<void>[] = [];
    this.clips.forEach((clip) => {
      task.push(clip.destroy());
    });
    await Promise.all(task).catch((error) => {
      alert(error.message);
    });
    task = [];
    this.clips.length = 0;
    this.clips = [];
    const canvas = this.painter.getDomElement();
    if (canvas) {
      canvas.parentNode?.removeChild(canvas);
    }
  }
}
