import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import { Radius10Button } from '../../../common/styleds';
import { formatMillisecond } from '../../../common/utils';
import Cut from '../../../icons/Cut';
import Delete from '../../../icons/Delete';
import Redo from '../../../icons/Redo';
import Undo from '../../../icons/Undo';
import { PlayerContext } from '../../type';
import { deleteSelectedClip, isClipCanSplit, splitSelectedClip } from '../../util';
import { ClipOperators, StyledToolBar, Time, TimeLineZoom } from './styled';

type Props = {
  playerContext: PlayerContext;
};

const ToolBar: React.FC<Props> = ({ playerContext }) => {
  const editorContextValue = useContext(editorContext);
  const { totalDuration, currentTime, activeClip, tracks } = editorContextValue;
  const handleDelete = useCallback(() => {
    deleteSelectedClip(editorContextValue);
  }, [editorContextValue]);
  const handleSplitClip = useCallback(() => {
    splitSelectedClip(editorContextValue);
  }, [editorContextValue]);
  return (
    <StyledToolBar>

      <ClipOperators>
        <Radius10Button className="disabled">
          <Undo />
        </Radius10Button>
        <Radius10Button className="disabled">
          <Redo />
        </Radius10Button>
        <Radius10Button onClick={handleSplitClip} className={isClipCanSplit(tracks, currentTime, activeClip?.id) ? '' : 'disabled'}>
          <Cut />
        </Radius10Button>
        <Radius10Button onClick={handleDelete} className={`warn ${activeClip ? '' : 'disabled'}`}>
          <Delete />
        </Radius10Button>
      </ClipOperators>

      <Time>
        {formatMillisecond(currentTime, true)}
        <span style={{ color: '#455570' }}> / {formatMillisecond(totalDuration, true)}</span>
      </Time>

      <TimeLineZoom>
        
      </TimeLineZoom>

    </StyledToolBar>
  );
};

export default ToolBar;
