export function extractDuration(field: string) {
  try {
    const duration = field.split(',')[0].replace('Duration:', '');
    return duration.trim();
  } catch {
    return '';
  }
}

export function extractVideoInfo(field: string) {
  try {
    const infoItems = field.split('Video:')[1].replace(/\(.*?\)/gi, '').split(',');
    const resolution = infoItems[2].split('x');
    return {
      codec: infoItems[0].trim(),
      colorSpace: infoItems[1].trim(),
      resolution: {
        width: parseInt(resolution[0].trim()),
        height: parseInt(resolution[1].trim()),
      },
      fps: parseInt(infoItems[4].replace('fps', '').trim()),
    };
  } catch {
    return {};
  }
}

export function extractAudioInfo(field: string) {
  try {
    const infoItems = field.split('Audio:')[1].replace(/\(.*?\)/gi, '').split(',');
    return {
      codec: infoItems[0].trim(),
      Hz: parseInt(infoItems[1].replace('Hz', '').trim()),
    };
  } catch {
    return {};
  }
}

export function getAudioTempoChain(tempo: number): string {
  if (Math.abs(tempo - 1) <= 10e-6) {
    return '';
  }
  if (tempo >= 0.5 && tempo <= 2.0) {
    return `,atempo=${tempo}`;
  }
  if (tempo > 0 && tempo < 0.5) {
    let start = 0.5;
    let result = `,atempo=0.5`;
    while (start > tempo) {
      start *= 0.5;
      result += `,atempo=0.5`;
    }
    if (Math.abs(start - tempo) > 10e-6) {
      result += `,atempo=${(tempo / start).toFixed(2)}`;
    }
    return result;
  }
  if (tempo > 2.0) {
    let start = 2.0;
    let result = `,atempo=2.0`;
    while (start < tempo) {
      start *= 2.0;
      result += `,atempo=2.0`;
    }
    if (Math.abs(start - tempo) > 10e-6) {
      result += `,atempo=${(start / tempo).toFixed(2)}`;
    }
    return result;
  }
  return '';
}
