import { EditorContext, PlayerContext } from '../type';
import { deleteSelectedClip, splitSelectedClip } from '../util';
import { useKeyPress } from 'ahooks';

export const useDeleteClip = (editorContextValue: EditorContext) => {
  useKeyPress('Backspace', () => {
    deleteSelectedClip(editorContextValue);
  });
};

/**
 * 切分当前选中的clip
 */
export const useSplitClip = (editorContextValue: EditorContext) => {
  useKeyPress('ctrl.c', () => {
    splitSelectedClip(editorContextValue);
  });
};

/**
 * 播放/暂停
 */
 export const usePlayControl = (editorContextValue: EditorContext, playerContext: PlayerContext) => {
  useKeyPress(' ', () => {
    const { isPlaying } = editorContextValue;
    playerContext.handleIsPlayingChange(!isPlaying);
  });
};

export const useKeyboard = (editorContextValue: EditorContext, playerContext: PlayerContext) => {
  useDeleteClip(editorContextValue);
  useSplitClip(editorContextValue);
  usePlayControl(editorContextValue, playerContext);
};
