import React, { useCallback, useContext, useState } from 'react';
import { v4 } from 'uuid';
import { editorContext } from '../..';
import Draggable from '../../../common/components/Draggable';
import { MoveDirection } from '../../../common/components/Draggable/config';
import { DragInfo } from '../../../common/components/Draggable/type';
import { Asset } from '../../../common/types/asset';
import { AudioClip, Clip, ClipType, GifClip, ImageClip, LottieClip, TextClip, TransitionClip, VideoClip } from '../../../common/types/clip';
import { getClipRenderInfo } from '../../../player/util';
import {
  adjustStartAndEnd,
  assetToClip,
  cursorPositionToTracksWrapperPosition,
  getAvailableStartAndEnd,
  getClipInsertIndex,
  getPlayerContainerBounding,
  getTrackAndClipIndex,
  getTrackIndex,
  getTrackInfoForDrag,
  millisecondsToWidth,
  needShowPrepareTrack,
  widthToMilliseconds,
} from '../../util';
import AudioCard from './components/AudioCard';
import GifCard from './components/GifCard';
import ImageCard from './components/ImageCard';
import LottieCard from './components/LottieCard';
import TextCard from './components/TextCard';
import TransitionCard from './components/TransitionCard';
import VideoCard from './components/VideoCard';
import { StyledCard } from './styled';

function getAssetCard(data: Clip | undefined, handleLoaded: (data: Clip) => void) {
  switch (data?.type) {
    case ClipType.VIDEO:
      return <VideoCard data={data as VideoClip} onLoaded={handleLoaded} />;
    case ClipType.IMAGE:
      return <ImageCard data={data as ImageClip} onLoaded={handleLoaded} />;
    case ClipType.AUDIO:
      return <AudioCard data={data as AudioClip} onLoaded={handleLoaded} />;
    case ClipType.TRANSITION:
        return <TransitionCard data={data as TransitionClip} onLoaded={handleLoaded} />;
    case ClipType.GIF:
      return <GifCard data={data as GifClip} onLoaded={handleLoaded} />;
    case ClipType.TEXT:
      return <TextCard data={data as TextClip} onLoaded={handleLoaded} />;
    case ClipType.LOTTIE:
      return <LottieCard data={data as LottieClip} onLoaded={handleLoaded} />;
    default:
      return null;
  }
}

type Props = {
  data: Asset;
};

const AssetCard: React.FC<Props> = ({ data }) => {
  const {
    setHoverTrack,
    tracks,
    setTracks,
    setNewTrackIndex,
    newTrackIndex,
    setDraggingClip,
  } = useContext(editorContext);

  const [loaded, setLoaded] = useState(false);
  const [clip, setClip] = useState<Clip>(assetToClip(data));

  const handleStart = useCallback(() => {
    setDraggingClip(clip);
  }, [clip, setDraggingClip]);

  const handleMove = useCallback((dragInfo: DragInfo<Asset>) => {
    const { belowElement, cursorLeft, cursorTop } = dragInfo;
    if (!belowElement) {
      return;
    }
    if (clip?.type === ClipType.TRANSITION) {
      return;
    }
    const { trackElement, index: trackIndex, isTop, isBottom } = getTrackInfoForDrag(belowElement, cursorLeft, cursorTop);
    if (trackElement && trackIndex >= 0) {
      const showPrepareTrack = needShowPrepareTrack(tracks);
      if (isTop && showPrepareTrack) {
        setNewTrackIndex(trackIndex);
      } else if (isBottom && showPrepareTrack) {
        setNewTrackIndex(trackIndex + 1);
      } else {
        setNewTrackIndex(-1);
      }
    }
    setHoverTrack(trackElement as HTMLElement);
  }, [clip?.type, setHoverTrack, setNewTrackIndex, tracks]);

  /**
   * transition单独处理
   */
  const handleTransitionEnd = useCallback((dragInfo: DragInfo<Asset>) => {
    const { belowElement } = dragInfo;
    if (belowElement.classList.contains('transition-insert') && clip) {
      const fromId = belowElement.dataset.from;
      const toId = belowElement.dataset.to;
      const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, fromId);
      const from = tracks[trackIndex][clipIndex];
      const to = tracks[trackIndex][clipIndex + 1];
      const halfTransitionDuration = clip.positionInTimeline.duration / 2;
      const newClip: TransitionClip = {
        ...(clip as TransitionClip),
        id: v4(),
        positionInTimeline: {
          ...clip.positionInTimeline,
          start: from.positionInTimeline.end - halfTransitionDuration,
          end: from.positionInTimeline.end + halfTransitionDuration,
        },
        fromId,
        toId,
      };
      Object.assign(to, {
        positionInTimeline: {
          ...to.positionInTimeline,
          start: to.positionInTimeline.start - halfTransitionDuration,
          end: to.positionInTimeline.end - halfTransitionDuration,
        },
      });
      tracks[trackIndex].splice(clipIndex + 1, 0, newClip);
      setTracks([...tracks]);
    }
    setDraggingClip(undefined);
    setNewTrackIndex(-1);
    setHoverTrack(null);
  }, [clip, setDraggingClip, setHoverTrack, setNewTrackIndex, setTracks, tracks]);

  const handleEnd = useCallback((dragInfo: DragInfo<Asset>) => {
    const { belowElement, cursorLeft, cursorTop, element } = dragInfo;
    if (!belowElement) {
      return;
    }
    if (clip?.type === ClipType.TRANSITION) {
      handleTransitionEnd(dragInfo);
      return;
    }
    const { trackElement } = getTrackInfoForDrag(belowElement, cursorLeft, cursorTop);
    const pos = cursorPositionToTracksWrapperPosition({ x: cursorLeft, y: cursorTop });
    let trackIndex = -1;
    if (newTrackIndex > -1) {
      trackIndex = newTrackIndex;
      tracks.splice(trackIndex, 0, []);
    } else if (trackElement) {
      trackIndex = getTrackIndex(trackElement as HTMLElement);
    }
    const track = tracks[trackIndex];
    if (trackIndex > -1 && track) {
      // 获取将要插入的索引
      const prepareInsertIndex = getClipInsertIndex(widthToMilliseconds(pos.x), track);
      if (prepareInsertIndex > -1) {
        // 获取可供插入的范围
        const availableDuration = getAvailableStartAndEnd(prepareInsertIndex, track);
        if (availableDuration.start || availableDuration.end) {
          const duration = clip?.positionInTimeline?.duration || 0;
          const width = millisecondsToWidth(duration); // 拖拽的元素放到track上的宽度
          const dragElementWidth = element.clientWidth;
          const shiftX = cursorLeft - dragInfo.left;
          const start = widthToMilliseconds(Math.max(0, pos.x - (shiftX / dragElementWidth) * width));
          const [finalStart, finalEnd] = adjustStartAndEnd(
            [availableDuration.start, availableDuration.end],
            [start, start + duration]
          );
          const { height: playerHeight, width: playerWidth } = getPlayerContainerBounding();
          const { width: renderWidth, height: renderHeight, left, bottom } = getClipRenderInfo(clip, playerWidth, playerHeight);
          const newClip: Clip = {
            ...clip,
            id: v4(),
            positionInTimeline: {
              start: finalStart,
              end: finalEnd,
              duration: finalEnd - finalStart,
            },
            positionInRender: {
              ...clip.positionInRender,
              width: renderWidth,
              height: renderHeight,
              left,
              bottom,
            },
          };
          track.splice(prepareInsertIndex, 0, newClip);
          setTracks([...tracks]);
        }
      }
    }
    setDraggingClip(undefined);
    setNewTrackIndex(-1);
    setHoverTrack(null);
  }, [clip, handleTransitionEnd, newTrackIndex, setDraggingClip, setHoverTrack, setNewTrackIndex, setTracks, tracks]);

  const handleLoaded = useCallback((data: Clip) => {
    setClip({ ...clip, ...data });
    setLoaded(true);
  }, [clip]);

  return (
    <Draggable
      direction={MoveDirection.ALL}
      scrollContainerSelector=".tracks-wrapper"
      autoGrow={true}
      enable={loaded}
      slow={2}
      onStart={handleStart}
      onMove={handleMove}
      onEnd={handleEnd}
    >
      <StyledCard>
        {getAssetCard(clip, handleLoaded)}
      </StyledCard>
    </Draggable>
  );
};

export default AssetCard;
