import React, { useEffect, useState } from 'react';
import { formatMillisecond } from '../../../common/utils';
import { SECOND_WIDTH } from '../../config';
import { StyledRuler, StyledRulerItem } from './styled';

const Ruler: React.FC = () => {
  const [width, setWidth] = useState(0);
  useEffect(() => {
    const tracksWrapper = document.querySelector('.tracks-wrapper') as HTMLElement;
    if (tracksWrapper) {
      setWidth(tracksWrapper.clientWidth);
      const observer = new ResizeObserver(() => {
        const tracksWrapperWidth = tracksWrapper.clientWidth;
        if (tracksWrapperWidth !== width) {
          setWidth(tracksWrapper.clientWidth);
        }
      });
      observer.observe(tracksWrapper)
      return () => {
        observer.disconnect();
      };
    }
  }, [width]);
  return (
    <StyledRuler style={{ width }}>
      {
        Array.from(Array(Math.ceil(width / SECOND_WIDTH))).map((value, index) => {
          return (
            <StyledRulerItem key={index} style={{ width: SECOND_WIDTH }}>
              {formatMillisecond(index * 1000, false, false)}
            </StyledRulerItem>
          );
        })
      }
    </StyledRuler>
  );
};

export default Ruler;
