import { ParsedFrame } from 'gifuct-js';
import React, { memo, useEffect, useRef } from 'react';
import { GifClip } from '../../../../../../../common/types/clip';
import WebWorker from '../../../../../../../workers/gifToCanvas.worker.ts';
import { TRACK_HEIGHT } from '../../../../../../config';
import { millisecondsToWidth } from '../../../../../../util';

type Props = {
  clip: GifClip;
};

async function drawToCanvas(
  worker: WebWorker,
  snapCount: number,
  from: number,
  to: number,
  frames: ParsedFrame[],
) {
  worker.postMessage({
    snapCount,
    from,
    to,
    frames,
  });
}

const GifTrack: React.FC<Props> = ({ clip }) => {
  const { positionInTimeline, originalWidth, originalHeight, frames } = clip;
  const snapCount = Math.ceil(millisecondsToWidth(positionInTimeline.duration) / TRACK_HEIGHT);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const workerRef = useRef<WebWorker>(new WebWorker());

  useEffect(() => {
    const canvas = canvasRef.current;
    const worker = workerRef.current;
    if (canvas && worker) {
      // @ts-ignore
      const offscreenCanvas = canvas.transferControlToOffscreen();
      worker.postMessage({
        canvas: offscreenCanvas,
      }, [offscreenCanvas]);
    }
  }, []);
  useEffect(() => {
    const canvas = canvasRef.current;
    const worker = workerRef.current;
    if (canvas && worker) {
      drawToCanvas(worker, snapCount, clip.playRange.start, clip.playRange.end, frames);
    }
  }, [frames, clip.playRange.end, clip.playRange.start, snapCount]);
  return (
    <div style={{ width: '100%' }}>
      <div style={{ position: 'absolute', left: 0, right: 0, top: 0, bottom: 0 }}>
        <canvas
          ref={canvasRef}
          style={{ transformOrigin: 'left top', transform: `scale(${TRACK_HEIGHT / originalHeight})` }}
          width={snapCount * originalWidth}
          height={originalHeight}
        />
      </div>
    </div>
  );
};

export default memo(GifTrack);
