import { AudioClip } from '../../common/types/clip';
import { ClipBase } from './base';

export class Audio extends ClipBase<AudioClip> {
  private context = new AudioContext();
  private audioBuffer?: AudioBuffer;
  private source?: AudioBufferSourceNode;
  public playbackRate = 1;

  private isPlaying: boolean = false;

  public async init() {
    try {
      this.audioBuffer = await this.getAudioBuffer();
      this.updatePlaybackRate();
    } catch (error) {
      alert(error);
    }
  }

  private getAudioBuffer(): Promise<AudioBuffer> {
    return new Promise((resolve, reject) => {
      this.context.decodeAudioData(this.config.buffer.slice(0), (buffer) => {
        if (buffer) {
          resolve(buffer);
        } else {
          reject(false);
        }
      }, (error) => {
        reject(error);
      });
    });
  }

  public play() {
    if (this.audioBuffer) {
      const source = this.context.createBufferSource();
      source.buffer = this.audioBuffer;
      source.loop = false;
      source.start(0, (this.config.playRange.start + (this.player.currentTime - this.config.positionInTimeline.start) * this.playbackRate) / 1000);
      this.isPlaying = true;
      this.source = source;
      this.source.connect(this.context.destination);
    }
  }

  public pause() {
    if (this.source) {
      this.source.stop();
      this.isPlaying = false;
    }
  }

  public updatePlaybackRate() {
    const originalDuration = this.config.playRange.end - this.config.playRange.start;
    this.playbackRate = originalDuration / this.config.positionInTimeline.duration;
  }

  public async onEnter() {
    if (this.player.isPlaying) {
      this.play();
    }
  }

  public async onLeave() {
    if (this.player.isPlaying) {
      this.pause();
    }
  }

  public async onUpdate(isManual: boolean) {
    if (isManual) {
      this.pause();
      if (this.player.isPlaying) {
        this.play();
      }
    }
  }

  public async destroy() {
    super.destroy();
    this.source?.stop();
  }
}
