import { ObjectType } from '.';

export enum FitType {
  CUSTOM,
  /** 铺满整个播放器，不考虑长宽比 */
  FILL,
  /** 保持长宽比，不会被裁剪，可能没法铺满整个播放器 */
  CONTAIN,
  /** 铺满整个播放器，同时保持长宽比，可能存在裁剪 */
  COVER,
}

export enum EffectType {
  NO_EFFECT,
  BLUR,
  GRAY,
}

export interface Effect {
  uniforms: ObjectType;
  fragment: (texName?: string, funName?: string) => string;
}

export enum TransitionType {
  MORPH,
  DREAMY,
  FADE,
}

export enum OverlayType {
  PLUNGING,
}
