import { ClipBase } from './base';
import * as THREE from 'three';
import { LottieClip } from '../../common/types/clip';
import { watch } from '../../common/utils/watch';
import { AnimationItem } from 'lottie-web';
import { getPixelRatio, loadLottie, setStyles } from '../../common/utils';
import effects from '../effects';
import { calcUVMap, Shader } from '../painter/shader';

export class Lottie extends ClipBase<LottieClip> {
  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.ShaderMaterial;
  private lottieInstance?: AnimationItem;
  private container: HTMLDivElement = document.createElement('div');

  public async init() {
    setStyles(this.container, {
      width: `${this.config.originalWidth * getPixelRatio()}px`,
      height: `${this.config.originalHeight * getPixelRatio()}px`,
    });
    document.body.appendChild(this.container);
    await this.initLottie();
    this.initRender();
    this.watchChange();
  }

  private watchChange() {
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
    watch(this.config, 'effect', (val) => {
      if (this.material) {
        const { uniforms, fragment } = effects(val)(this);
        this.material.uniforms = {
          ...this.material.uniforms,
          ...uniforms,
        };
        this.material.fragmentShader = Shader.GetEffectFragment({
          effect: fragment(),
        });
        this.material.uniformsNeedUpdate = true;
        this.material.needsUpdate = true;
      }
    });
    watch(this.config, 'positionInRender', (val) => {
      if (this.material) {
        const { width: currentWidth, height: currentHeight, left, bottom } = this.config.positionInRender;
        const { player, config } = this;
        const [uRange, vRange] = calcUVMap(
          val.fitType,
          player.width,
          player.height,
          currentWidth,
          currentHeight,
          config.originalWidth,
          config.originalHeight,
          new THREE.Vector2(left, bottom),
        );
        // @ts-ignore
        this.material.uniforms.uRange.value = uRange;
        // @ts-ignore
        this.material.uniforms.vRange.value = vRange;
        this.material.uniformsNeedUpdate = true;
      }
    });
  }

  private async initLottie() {
    this.lottieInstance = await loadLottie(this.container, this.config.animationData);
  }

  private initRender() {
    const canvas = this.container.querySelector('canvas');
    if (this.lottieInstance && canvas) {
      const {
        positionInRender: { width: currentWidth, height: currentHeight, fitType, left, bottom },
        effect,
        originalWidth,
        originalHeight,
      } = this.config;
      const { player } = this;
      const { uniforms, fragment } = effects(effect)(this);
      const [uRange, vRange] = calcUVMap(
        fitType,
        player.width,
        player.height,
        currentWidth,
        currentHeight,
        originalWidth,
        originalHeight,
        new THREE.Vector2(left, bottom),
      );
      this.texture = new THREE.CanvasTexture(canvas);
      this.geometry = new THREE.PlaneGeometry(player.width, player.height);
      this.material = new THREE.ShaderMaterial({
        uniforms: {
          tex: { value: this.texture },
          uRange: { value: uRange },
          vRange: { value: vRange },
          ...uniforms,
        },
        vertexShader: Shader.GetVertex(),
        fragmentShader: Shader.GetEffectFragment({
          effect: fragment(),
        }),
      });
      this.material.uniformsNeedUpdate = true;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
      this.sprite.position.z = this.config.zIndex;
    }
  }

  public async onUpdate() {
    if (this.texture && this.lottieInstance) {
      const { player: { currentTime } } = this;
      this.lottieInstance.goToAndStop(currentTime - this.config.positionInTimeline.start);
      this.texture.needsUpdate = true;
    }
  }

  public async destroy() {
    super.destroy();
    this.lottieInstance?.destroy();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}