import React, { memo, useCallback, useContext } from 'react';
import { editorContext } from '../../../..';
import Draggable from '../../../../../common/components/Draggable';
import { MoveDirection } from '../../../../../common/components/Draggable/config';
import { DragInfo } from '../../../../../common/components/Draggable/type';
import { Asset } from '../../../../../common/types/asset';
import { AudioClip, Clip, ClipType, GifClip, ImageClip, VideoClip } from '../../../../../common/types/clip';
import { TRACK_OUTER_HEIGHT } from '../../../../config';
import {
  adjustStartAndEnd,
  cursorPositionToTracksWrapperPosition,
  getAvailableStartAndEnd,
  getClipInsertIndex,
  getTrackAndClipIndex,
  getTrackInfoForDrag,
  isTransitionEnd,
  isTransitionStart,
  millisecondsToWidth,
  needShowPrepareTrack,
  widthToMilliseconds,
} from '../../../../util';
import AudioTrack from './components/AudioTrack';
import GifTrack from './components/GifTrack';
import ImageTrack from './components/ImageTrack';
import VideoTrack from './components/VideoTrack';
import { StyledHandle, StyledName, StyledTrackElement } from './styled';

const transitionStyle = {
  zIndex: 999,
  border: '2px solid #fff',
};

function getTrackComponent(clip: Clip) {
  switch (clip.type) {
    case ClipType.VIDEO:
      return <VideoTrack clip={clip as VideoClip} />;
    case ClipType.GIF:
      return <GifTrack clip={clip as GifClip} />;
    case ClipType.AUDIO:
      return <AudioTrack clip={clip as AudioClip} />;
    case ClipType.IMAGE:
      return <ImageTrack clip={clip as ImageClip} />;
    default:
      return <StyledName>{clip.name}</StyledName>;
  }
}

interface Props {
  clip: Clip;
  trackIndex: number;
  onUpdate?(data: Partial<Clip>, trackIndex: number, clipIndex: number, isNewTrack: boolean): void;
}

const TrackElement: React.FC<Props> = ({ clip, onUpdate, trackIndex }) => {
  const { positionInTimeline, id, type } = clip;
  const {
    setHoverTrack,
    tracks,
    setTracks,
    setNewTrackIndex,
    newTrackIndex,
    activeClip,
    setActiveClip,
  } = useContext(editorContext);

  const handleTransition = useCallback((dragInfo: DragInfo<Asset>) => {
    if (dragInfo.stepLeft !== 0 || dragInfo.stepTop !== 0) {
      const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
      if (isTransitionStart(tracks, id)) {
        // 删除transition
        tracks[trackIndex].splice(clipIndex + 1, 1);
        setTracks([...tracks]);
      } else if (isTransitionEnd(tracks, id)) {
        // 删除transition
        tracks[trackIndex].splice(clipIndex - 1, 1);
        setTracks([...tracks]);
      }
    }
  }, [id, setTracks, tracks]);

  const handleMove = useCallback((dragInfo: DragInfo<Asset>) => {
    if (!dragInfo.belowElement) {
      return;
    }

    handleTransition(dragInfo);

    const { trackElement, index: trackIndex, isTop, isBottom } = getTrackInfoForDrag(dragInfo.belowElement, dragInfo.cursorLeft, dragInfo.cursorTop);
    if (trackElement && trackIndex >= 0) {
      const showPrepareTrack = needShowPrepareTrack(tracks);
      if (isTop && showPrepareTrack) {
        setNewTrackIndex(trackIndex);
      } else if (isBottom && showPrepareTrack) {
        setNewTrackIndex(trackIndex + 1);
      } else {
        setNewTrackIndex(-1);
      }
    }
    setHoverTrack(trackElement as HTMLElement);
  }, [handleTransition, setHoverTrack, setNewTrackIndex, tracks]);

  const handleEnd = useCallback((dragInfo: DragInfo<Asset>) => {
    if (!dragInfo.belowElement) {
      return;
    }
    if (dragInfo.isMoved) {
      const { trackElement, index: trackIndex } = getTrackInfoForDrag(dragInfo.belowElement, dragInfo.cursorLeft, dragInfo.cursorTop);
      const pos = cursorPositionToTracksWrapperPosition({ x: dragInfo.cursorLeft, y: dragInfo.cursorTop });
      let newStart = widthToMilliseconds(Math.max(0, pos.x - (dragInfo.cursorLeft - dragInfo.left)));
      let updated = false;
      if (newTrackIndex > -1) {
        updated = true;
        onUpdate?.({
          positionInTimeline: {
            ...positionInTimeline,
            start: newStart, 
            end: newStart + positionInTimeline.duration 
          },
        }, newTrackIndex, 0, true);
      } else if (trackElement) {
        const { trackIndex: originalTrackIndex } = getTrackAndClipIndex(tracks, id);
        let track = tracks[trackIndex];
        if (trackIndex === originalTrackIndex) {
          track = track.filter((item) => item.id !== id);
        }
        if (trackIndex > -1 && track) {
          const prepareInsertIndex = getClipInsertIndex(widthToMilliseconds(pos.x), track);
          if (prepareInsertIndex > -1) {
            const availableDuration = getAvailableStartAndEnd(prepareInsertIndex, track);
            if (availableDuration.start || availableDuration.end) {
              const [start, end] = adjustStartAndEnd(
                [availableDuration.start, availableDuration.end],
                [newStart, newStart + positionInTimeline.duration]
              );
              updated = true;
              onUpdate?.({
                positionInTimeline: {
                  start,
                  end,
                  duration: end - start,
                },
              }, trackIndex, prepareInsertIndex, false);
            }
          }
        }
      }
      // 删除transition之后可能会错位，需要修正
      if (!updated) {
        const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
        const before = tracks[trackIndex][clipIndex - 1];
        const after = tracks[trackIndex][clipIndex + 1];
        if ((before && before.positionInTimeline.end > positionInTimeline.start) || (after && after.positionInTimeline.start < positionInTimeline.end)) {
          const newRange = adjustStartAndEnd(
            [before?.positionInTimeline?.end || 0, after?.positionInTimeline?.start || 10e9],
            [positionInTimeline.start, positionInTimeline.end]
          );
          onUpdate?.({
            positionInTimeline: {
              start: newRange[0],
              end: newRange[1],
              duration: newRange[1] - newRange[0],
            },
          }, trackIndex, clipIndex, false);
        }
      }
    }
    setNewTrackIndex(-1);
    setHoverTrack(null);
  }, [id, newTrackIndex, onUpdate, positionInTimeline, setHoverTrack, setNewTrackIndex, tracks]);

  const handleLeftHandleMove = useCallback((dragInfo: DragInfo<Asset>) => {
    const newStart = positionInTimeline.start + widthToMilliseconds(dragInfo.stepLeft);
    if (positionInTimeline.end - newStart >= 500) {
      const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
      const preClipEnd = tracks[trackIndex][clipIndex - 1]?.positionInTimeline?.end || 0;
      onUpdate?.({
        positionInTimeline: {
          ...positionInTimeline,
          start: Math.max(newStart, preClipEnd),
          duration: positionInTimeline.end - Math.max(newStart, preClipEnd),
        },
      }, trackIndex, clipIndex, false);
    }
  }, [id, onUpdate, positionInTimeline, tracks]);

  const handleRightHandleMove = useCallback((dragInfo: DragInfo<Asset>) => {
    const newEnd = positionInTimeline.end + widthToMilliseconds(dragInfo.stepLeft);
    if (newEnd - positionInTimeline.start >= 500) {
      const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
      const nextClipStart = tracks[trackIndex][clipIndex + 1]?.positionInTimeline?.start || 10e9;
      onUpdate?.({
        positionInTimeline: {
          ...positionInTimeline,
          end: Math.min(newEnd, nextClipStart),
          duration: Math.min(newEnd, nextClipStart) - positionInTimeline.start,
        },
      }, trackIndex, clipIndex, false);
    }
  }, [positionInTimeline, tracks, id, onUpdate]);

  /**
   * 拖拽结束的时候更新duration
   */
  const handleHandleEnd = useCallback(() => {
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
    onUpdate?.({
      positionInTimeline: {
        ...positionInTimeline,
        duration: positionInTimeline.end - positionInTimeline.start,
      },
    }, trackIndex, clipIndex, false);
  }, [id, onUpdate, positionInTimeline, tracks]);

  const handleClick = useCallback((e: React.MouseEvent) => {
    e.stopPropagation();
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, id);
    setActiveClip(tracks?.[trackIndex]?.[clipIndex]);
  }, [id, setActiveClip, tracks]);

  const isTransition = type === ClipType.TRANSITION;
  const isActive = activeClip?.id === id;

  return (
    <Draggable
      scrollContainerSelector=".tracks-wrapper"
      autoGrow={true}
      slow={2}
      onMove={handleMove}
      onEnd={handleEnd}
      enable={!isTransition}
    >
      <StyledTrackElement
        className={`track-element ${isActive ? 'active' : ''}`}
        onClick={handleClick}
        style={{
          top: `calc(50% - ${tracks.length * 70 / 2}px + 5px + ${trackIndex * 70}px)`,
          width: millisecondsToWidth(positionInTimeline.end - positionInTimeline.start),
          height: TRACK_OUTER_HEIGHT,
          left: millisecondsToWidth(positionInTimeline.start),
          ...( type === ClipType.TRANSITION ? transitionStyle : null ),
        }}
      >
        {
          !isTransition && !isTransitionEnd(tracks, id) && isActive ? <StyledHandle>
            <Draggable
              direction={MoveDirection.EW}
              onMove={handleLeftHandleMove}
              onEnd={handleHandleEnd}
            >
              <div style={{ width: '100%', height: '100%' }}></div>
            </Draggable>
          </StyledHandle> : null
        }
        {getTrackComponent(clip)}
        {
          !isTransition && !isTransitionStart(tracks, id) && isActive ? <StyledHandle>
            <Draggable
              direction={MoveDirection.EW}
              onMove={handleRightHandleMove}
              onEnd={handleHandleEnd}
            >
              <div style={{ width: '100%', height: '100%' }}></div>
            </Draggable>
          </StyledHandle> : null
        }
      </StyledTrackElement>
    </Draggable>
  );
};

export default memo(TrackElement);
