import React, { useEffect } from 'react';
import { parseGIF, decompressFrames } from 'gifuct-js';
import { GifClip } from '../../../../../common/types/clip';
import { StyledAssetCard } from '../../../../../common/styleds';

type Props = {
  data: GifClip;
  onLoaded(data: GifClip): void;
};

const GifCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data?.frames?.length) {
      fetch(data.url).then((response) => {
        return response.arrayBuffer();
      }).then((buffer: ArrayBuffer) => {
        const gif = parseGIF(buffer);
        const frames = decompressFrames(gif, true);
        let duration = 0;
        frames.forEach((frame) => {
          duration += frame.delay;
        });
        onLoaded({
          ...data,
          positionInTimeline: {
            ...data.positionInTimeline,
            duration,
          },
          positionInRender: {
            ...data.positionInRender,
            width: gif.lsd.width,
            height: gif.lsd.height,
          },
          playRange: {
            start: 0,
            end: duration,
          },
          frames,
          originalWidth: gif.lsd.width,
          originalHeight: gif.lsd.height,
        });
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <img src={data.url} alt="" draggable="false" />
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default GifCard;
