import React, { ReactElement, useCallback, useEffect, useRef, useState } from 'react';
import { setStyles } from '../../utils';
import { MoveDirection } from './config';
import { DragInfo } from './type';

type Props<T> = {
  children: React.ReactElement;
  direction?: MoveDirection;
  scrollContainerSelector?: string;
  autoGrow?: boolean;
  enable?: boolean;
  attachData?: T;
  slow?: number;
  onStart?(): void;
  onMove?(dragInfo: DragInfo<T>): void;
  onEnd?(dragInfo: DragInfo<T>): void;
};

function isLeftButton(e: MouseEvent) {
  if (e.metaKey || e.ctrlKey || e.altKey || e.shiftKey) {
    return false;
  } else if ('buttons' in e) {
    return e.buttons === 1;
  } else if ('which' in e) {
    return e.which === 1;
  }
  return (e.button === 1 || e.type === 'click');
}

const Draggable = <T extends {}>({
  children,
  direction = MoveDirection.ALL,
  scrollContainerSelector,
  autoGrow,
  enable = true,
  attachData,
  slow = 1,
  onStart,
  onMove,
  onEnd,
}: Props<T>): JSX.Element => {
  const childRef = useRef<HTMLElement | null>(null);
  const shiftX = useRef(0);
  const shiftY = useRef(0);
  const timer = useRef(0);
  const originalStyle = useRef('');
  const originalLeft = useRef(0);
  const originalTop = useRef(0);
  const preLeft = useRef(0);
  const preTop = useRef(0);
  const isEnd = useRef(false);
  const count = useRef(0);
  const isMoved = useRef(false);
  const isHoverScrollContainer = useRef(false);

  const [dragInfo, setDragInfo] = useState<DragInfo<T>>();
  useEffect(() => {
    if (dragInfo) {
      if (isEnd.current) {
        count.current = 0;
        onEnd?.(dragInfo);
      } else {
        count.current = count.current + 1;
        (count.current % slow) === 0 && onMove?.(dragInfo);
      }
      setDragInfo(undefined);
    }
  }, [dragInfo, onEnd, onMove, slow]);

  /**
   * 更新拖拽元素的位置
   */
  const move = useCallback((el: HTMLElement, pageX: number, pageY: number) => {
    let transformStyle = '';
    if (MoveDirection.ALL === direction) {
      transformStyle = `translate(${pageX - shiftX.current}px, ${pageY - shiftY.current}px)`;
    }
    if (MoveDirection.EW === direction) {
      transformStyle = `translate(${pageX - shiftX.current}px, ${originalTop.current}px)`;
    }
    if (MoveDirection.NS === direction) {
      transformStyle = `translate(${originalLeft.current}px, ${pageY - shiftY.current}px)`;
    }
    el.style.transform = transformStyle;
  }, [direction]);

  const handleHold = useCallback((step: number, wrapper: HTMLElement, scrollContainer: HTMLElement) => {
    timer.current = window.setInterval(() => {
      const wrapperRect = wrapper.getBoundingClientRect();
      if (step > 0 && autoGrow && (wrapper.scrollLeft + wrapperRect.width) > (scrollContainer.clientWidth - 100)) {
        scrollContainer.style.width = `${scrollContainer.clientWidth + 100}px`;
      }
      wrapper.scrollLeft += step * 2; // hold的时候加快速度
    }, 1000 / 60);
  }, [autoGrow]);

  /**
   * 更新指定容器的滚动位置
   */
  const handleScroll = useCallback((below: HTMLElement, pageX: number, pageY: number) => {
    if (timer) {
      window.clearInterval(timer.current);
      timer.current = 0;
    }
    if (scrollContainerSelector) {
      const scrollThreshold = 100;
      const scrollContainer = document.querySelector(scrollContainerSelector) as HTMLElement;
      if (!isHoverScrollContainer.current) {
        if (!below?.closest(scrollContainerSelector)) { // 如果不在滚动容器内，则不滚动
          return;
        } else {
          isHoverScrollContainer.current = true;
        }
      }
      if (scrollContainer?.parentElement) {
        const wrapper = scrollContainer.parentElement;
        const wrapperRect = wrapper.getBoundingClientRect();
        let step = 0;
        if (pageX > wrapperRect.right - scrollThreshold) { // 向右👉
          if (autoGrow && (wrapper.scrollLeft + wrapperRect.width) > (scrollContainer.clientWidth - 100)) {
            scrollContainer.style.width = `${scrollContainer.clientWidth + 100}px`;
          }
          step = 5 * (1 + Math.min((pageX - wrapperRect.right + scrollThreshold) / scrollThreshold, 1));
        } else if (pageX < wrapperRect.left + scrollThreshold) { // 👈向左
          step = -5 * (1 + Math.min((wrapperRect.left + scrollThreshold - pageX) / scrollThreshold, 1));
        }
        if (step !== 0) {
          wrapper.scrollLeft += step;
          handleHold(step, wrapper, scrollContainer);
        }
      }
    }
  }, [autoGrow, handleHold, scrollContainerSelector]);

  const handleMouseMove = useCallback((e: MouseEvent) => {
    const el = childRef.current;
    if (el) {
      el.hidden = true;
      const below = document.elementFromPoint(e.clientX, e.clientY) as HTMLElement;
      move(el, e.pageX, e.pageY);
      handleScroll(below, e.pageX, e.pageY);
      const currentLeft = e.pageX - shiftX.current;
      const currentTop = e.pageY - shiftY.current;
      const stepLeft = currentLeft - preLeft.current;
      const stepTop = currentTop - preTop.current;
      if (stepLeft !== 0 || stepTop !== 0) {
        isMoved.current = true;
        setDragInfo({
          element: el,
          belowElement: below,
          left: currentLeft,
          top: currentTop,
          originalLeft: originalLeft.current,
          originalTop: originalTop.current,
          stepLeft: currentLeft - preLeft.current,
          stepTop: currentTop - preTop.current,
          cursorLeft: e.pageX,
          cursorTop: e.pageY,
          attachData,
          isMoved: isMoved.current,
        });
        preLeft.current = currentLeft;
        preTop.current = currentTop;
      }
      el.hidden = false;
    }
  }, [move, handleScroll, attachData]);

  const handleMouseUp = useCallback((e: MouseEvent) => {
    const { pageX, pageY } = e;
    const el = childRef.current;
    if (el) {
      el.hidden = true;
      const below = document.elementFromPoint(e.clientX, e.clientY) as HTMLElement;
      el.hidden = false;
      el.classList.remove('dragged');
      el.setAttribute('style', originalStyle.current);
      isEnd.current = true;
      setDragInfo({
        element: el,
        belowElement: below,
        left: pageX - shiftX.current,
        top: pageY - shiftY.current,
        originalLeft: originalLeft.current,
        originalTop: originalTop.current,
        stepLeft: 0,
        stepTop: 0,
        cursorLeft: pageX,
        cursorTop: pageY,
        attachData,
        isMoved: isMoved.current,
      });
    }
    window.clearInterval(timer.current);
    window.removeEventListener('mousemove', handleMouseMove, false);
    window.removeEventListener('mouseup', handleMouseUp, false);
  }, [attachData, handleMouseMove]);

  const handleMouseDown = useCallback((e: MouseEvent) => {
    e.stopPropagation();
    if (!isLeftButton(e)) {
      return;
    }
    if (childRef.current) {
      isEnd.current = false;
      const el = childRef.current;
      const { left, top, width, height } = el.getBoundingClientRect();
      shiftX.current = e.clientX - left;
      shiftY.current = e.clientY - top;

      // 保留原样式
      originalStyle.current = el.getAttribute('style') || '';
      originalLeft.current = left;
      originalTop.current = top;
      preLeft.current = left;
      preTop.current = top;
      isHoverScrollContainer.current = false;
      isMoved.current = false;

      setStyles(el, {
        transform: `translate(${left}px, ${top}px)`,
        width: `${width}px`,
        height: `${height}px`,
      });
      el.classList.add('dragged');
      onStart?.();
      move(el, e.pageX, e.pageY);
      window.addEventListener('mousemove', handleMouseMove, false);
      window.addEventListener('mouseup', handleMouseUp, false);
    }
  }, [handleMouseMove, handleMouseUp, move, onStart]);

  useEffect(() => {
    const container = childRef.current;
    if (container && enable) {
      container.addEventListener('mousedown', handleMouseDown, false);
      return () => {
        container.removeEventListener('mousedown', handleMouseDown, false);
      };
    }
  }, [childRef, enable, handleMouseDown]);
  return (
    <>
      {
        React.cloneElement(children as ReactElement, {
          ref: (ref: HTMLElement) => (childRef.current = ref),
        })
      }
    </>
  );
};

export default Draggable;
