/**
 * 
 * @param belowEl 当前光标下方的元素
 * @param cursorLeft 光标距离屏幕左方距离
 * @param cursorTop 光标距离屏幕上方距离
 * @returns { isNew: number, index: number, trackElement: HTMLElement }
 */
export function getTrackInfoForDrag(belowEl: HTMLElement, cursorLeft: number, cursorTop: number) {
  const tracksWrapper = belowEl.closest('.tracks-wrapper');
  if (tracksWrapper) {
    const trackList = tracksWrapper.querySelectorAll(`.${TRACK_WRAPPER_CLASS_NAME}`);
    for (let i = 0; i < trackList.length; i++) {
      const { y, height } = trackList[i].getBoundingClientRect();
      if (cursorTop >= y && cursorTop <= (y + height)) {
        if (cursorTop >= y && cursorTop <= (y + NEW_TRACK_THRESHOLD)) { // 顶部边缘
          return {
            isTop: true,
            index: i,
            trackElement: trackList[i],
          };
        } else if (cursorTop <= (y + height) && cursorTop >= (y + height - NEW_TRACK_THRESHOLD)) { // 下部边缘
          return {
            isBottom: true,
            index: i,
            trackElement: trackList[i],
          };
        } else if (cursorTop >= y && cursorTop <= (y + height)) { // 内部
          return {
            isNew: false,
            index: i,
            trackElement: trackList[i],
          };
        } 
      }
    }
  }
  return {
    index: -1,
    trackElement: null,
  };
}

/**
 * 获取trackElement的trackIndex和clipIndex
 */
export function getTrackAndClipIndex(data: Clip[][], id?: string) {
  for (let i = 0; i < data.length; i++) {
    for (let j = 0; j < data[i].length; j++) {
      if (data[i][j].id === id) {
        return { trackIndex: i, clipIndex: j };
      }
    }
  }
  return { trackIndex: -1, clipIndex: -1 };
}

export function needShowPrepareTrack(data: Clip[][]) {
  return !(data.length === 0 || (data.length === 1 && data[0].length === 0));
}

export function millisecondsToWidth(milliseconds?: number) {
  return (milliseconds || 0) / 1000 * SECOND_WIDTH;
}

export function widthToMilliseconds(width: number) {
  return Math.round(width / SECOND_WIDTH * 1000);
}

export function getClipInsertIndex(cursorX: number, data: Clip[]) {
  if (data.length) {
    for (let i = 0; i < data.length; i++) {
      const { start, end } = data[i].positionInTimeline;
      if (cursorX >= start && cursorX <= end) {
        return -1;
      }
      if (cursorX < start) {
        return i;
      }
    }
    if (cursorX > data[data.length - 1].positionInTimeline.end) {
      return data.length;
    }
  }
  return 0;
}

export function getAvailableStartAndEnd(prepareInsertIndex: number, data: Clip[]) {
  const pre = data[prepareInsertIndex - 1];
  const next = data[prepareInsertIndex];
  if (!pre && !next) {
    return { start: 0, end: 10e9 };
  }
  if (!pre && next) {
    return { start: 0, end: next.positionInTimeline.start };
  }
  if (pre && next) {
    return { start: pre.positionInTimeline.end, end: next.positionInTimeline.start };
  }
  if (pre && !next) {
    return { start: pre.positionInTimeline.end, end: 10e9 };
  }
  return { start: 0, end: 0 };
}

/**
 * 调整插入范围
 * @param availableRange 当前可供插入的范围
 * @param insertRange 要插入的范围
 */
export function adjustStartAndEnd(availableRange: Range, insertRange: Range): Range {
  if (insertRange[0] >= availableRange[0] && insertRange[1] <= availableRange[1]) {
    return insertRange;
  }
  if (insertRange[0] >= availableRange[0] && insertRange[1] > availableRange[1]) { // 需要左移
    const diff = insertRange[1] - availableRange[1];
    return [
      Math.max(insertRange[0] - diff, availableRange[0]),
      Math.min(insertRange[1] - diff, availableRange[1]),
    ];
  }
  if (insertRange[0] < availableRange[0] && insertRange[1] <= availableRange[1]) { // 需要右移
    const diff = availableRange[0] - insertRange[0];
    return [
      Math.max(insertRange[0] + diff, availableRange[0]),
      Math.min(insertRange[1] + diff, availableRange[1]),
    ];
  }
  if (insertRange[0] <= availableRange[0] && insertRange[1] >= availableRange[1]) {
    return availableRange;
  }
  return insertRange;
}

/**
 * 获取当前编辑内容的总时长
 */
export function getTotalDuration(data: Clip[][]) {
  let result = 0;
  data.forEach((track) => {
    if (track?.length) {
      const lastClip = track[track.length - 1];
      result = Math.max(result, lastClip.positionInTimeline.end);
    }
  });
  return result;
}

/**
 * 判断两个clip是否相邻
 */
export function isClipsNext(clip1?: Clip, clip2?: Clip) {
  if (clip1 && clip2) {
    return Math.abs(clip1.positionInTimeline.end - clip2.positionInTimeline.start) <= 10e-6;
  }
  return false;
}

export function canWithTransition(clipType: ClipType) {
  return [
    ClipType.VIDEO,
    ClipType.IMAGE,
    ClipType.GIF,
  ].includes(clipType);
}

export function isTransitionStart(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).fromId === clipId) > -1;
}

export function isTransitionEnd(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).toId === clipId) > -1;
}

export function getTransition(tracks: Clip[][], clipId?: string) {
  const transitions = tracks.flat(1).filter((clip) => clip.type === ClipType.TRANSITION);
  return transitions.findIndex((transition) => (transition as TransitionClip).fromId === clipId) > -1;
}

export function deleteSelectedClip(editorContextValue: EditorContext) {
  const { tracks, activeClip: clip, setTracks, setActiveClip } = editorContextValue;
  if (clip) {
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, clip.id);
    // 如果是transition，需要更新toClip的position
    if (clip.type === ClipType.TRANSITION) {
      const fromClip = tracks[trackIndex][clipIndex - 1];
      const toClip = tracks[trackIndex][clipIndex + 1];
      const afterToClip = tracks[trackIndex][clipIndex + 2];
      const newRange = adjustStartAndEnd(
        [fromClip?.positionInTimeline?.end || 0, afterToClip?.positionInTimeline?.start || 10e9],
        [toClip.positionInTimeline.start + clip.positionInTimeline.duration / 2, toClip.positionInTimeline.end + clip.positionInTimeline.duration / 2]
      );
      Object.assign(toClip, {
        positionInTimeline: {
          start: newRange[0],
          end: newRange[1],
          duration: newRange[1] - newRange[0],
        },
      });
      tracks[trackIndex].splice(clipIndex, 1); // 删除clip
    } else if (isTransitionStart(tracks, clip.id)) {
      tracks[trackIndex].splice(clipIndex, 1); // 删除clip
      tracks[trackIndex].splice(clipIndex, 1); // 删除transition
    } else if (isTransitionEnd(tracks, clip.id)) {
      tracks[trackIndex].splice(clipIndex, 1); // 删除clip
      tracks[trackIndex].splice(clipIndex - 1, 1); // 删除transition
    } else {
      tracks[trackIndex].splice(clipIndex, 1); // 删除clip
    }
    setActiveClip(undefined);
    setTracks([...tracks]);
  }
}

export function isClipCanSplit(data: Clip[][], currentTime: number, id?: string) {
  const { trackIndex, clipIndex } = getTrackAndClipIndex(data, id);
  if (trackIndex > -1 && clipIndex > -1) {
    const clip = data[trackIndex][clipIndex];
    const canSplit = isClipTypeCanSplit(clip.type);
    const { start, end } = clip.positionInTimeline;
    return canSplit && start < currentTime && end > currentTime;
  }
  return false;
}

export function isClipTypeCanSplit(clipType: ClipType) {
  return [
    ClipType.AUDIO,
    ClipType.GIF,
    ClipType.IMAGE,
    ClipType.VIDEO,
  ].includes(clipType);
}

export function splitSelectedClip(editorContextValue: EditorContext) {
  const { tracks, activeClip: clip, setTracks, currentTime } = editorContextValue;
  if (clip) {
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, clip.id);
    const canSplit = isClipTypeCanSplit(clip.type);
    const { start, end } = clip.positionInTimeline;
    if (canSplit && start < currentTime && end > currentTime) {
      const splitPercent = (currentTime - start) / (end - start);
      const splitTime = Math.round((clip.playRange.end - clip.playRange.start) * splitPercent);
      // 需保持playRange在positionInTimeline之前，不然计算playbackRate会有bug
      tracks[trackIndex].splice(clipIndex + 1, 0, {
        ...clip,
        id: v4(),
        playRange: {
          start: splitTime,
          end: clip.playRange.end,
        },
        positionInTimeline: {
          start: currentTime,
          end,
          duration: end - currentTime,
        },
      });
      Object.assign(clip, {
        playRange: {
          start: clip.playRange.start,
          end: splitTime,
        },
        positionInTimeline: {
          start,
          end: currentTime,
          duration: currentTime - start,
        },
      });
      setTracks([...tracks]);
    }
  }
}

/**
 * 资源onLoad之前的初始化值
 */
 export function assetToClip(asset: Asset, start?: number, end?: number): Clip {
  const common = {
    name: asset.name,
    positionInTimeline: {
      start,
      end,
      duration: 0,
    },
    positionInRender: {
      fitType: FitType.CONTAIN,
      rotate: 0,
      left: 0,
      bottom: 0,
      width: 0,
      height: 0,
    },
    playRange: {
      start: 0,
      end: 0,
    },
    effect: EffectType.NO_EFFECT,
  };
  switch (asset.type) {
    case AssetType.VIDEO:
      return {
        ...common,
        type: ClipType.VIDEO,
        url: (asset as VideoAsset).url,
      } as VideoClip;
    case AssetType.IMAGE:
      return {
        ...common,
        type: ClipType.IMAGE,
        url: (asset as VideoAsset).url,
      } as ImageClip;
    case AssetType.AUDIO:
      return {
        ...common,
        type: ClipType.AUDIO,
        url: (asset as AudioAsset).url,
      } as AudioClip;
    case AssetType.TRANSITION:
      return {
        ...common,
        type: ClipType.TRANSITION,
        transition: (asset as TransitionAsset).transition,
      } as TransitionClip;
    case AssetType.GIF:
      return {
        ...common,
        positionInRender: {
          ...common.positionInRender,
          fitType: FitType.CUSTOM,
        },
        type: ClipType.GIF,
        url: (asset as VideoAsset).url,
        frames: [],
        originalWidth: 0,
        originalHeight: 0,
        zIndex: 0,
      } as GifClip;
    case AssetType.TEXT:
      return {
        ...common,
        positionInRender: {
          ...common.positionInRender,
          fitType: FitType.CUSTOM,
          left: 50,
          bottom: 50,
        },
        props: {
          fontColor: '#fff',
          fontSize: 16,
          fontFamily: 'Helvetica',
        },
        value: (asset as TextAsset).value,
        type: ClipType.TEXT,
        originalWidth: 0,
        originalHeight: 0,
        zIndex: 0,
      } as TextClip;
    case AssetType.LOTTIE:
      return {
        ...common,
        positionInRender: {
          ...common.positionInRender,
          fitType: FitType.CUSTOM,
          left: 0,
          bottom: 0,
        },
        url: (asset as LottieAsset).url,
        type: ClipType.LOTTIE,
      } as LottieClip;
    default:
      return {} as Clip;
  }
}

export function getPlayerContainerBounding(): DOMRect {
  const playerContainer = document.querySelector('.player');
  if (playerContainer) {
    return playerContainer.getBoundingClientRect();
  }
  return {
    width: 0,
    height: 0,
    x: 0,
    y: 0,
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    toJSON: () => {},
  };
}

/**
 * 根据比例和容器大小计算player长宽
 */
export function getPlayerResolution(ratio: Ratio, containerWidth: number, containerHeight: number) {
  const ratioValue = getRatioValue(ratio);
  let newWidth = 0;
  let newHeight = 0;
  if (ratioValue * containerHeight > containerWidth) {
    newWidth = Math.round(containerWidth);
    newHeight = Math.round(containerWidth / ratioValue);
  } else {
    newWidth = Math.round(ratioValue * containerHeight);
    newHeight = Math.round(containerHeight);
  }
  if (newWidth % 2 !== 0) {
    newWidth -= 1;
  }
  if (newHeight % 2 !== 0) {
    newHeight -= 1;
  }
  return {
    width: newWidth,
    height: newHeight,
  };
}
