import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import RatioSelector from '../../../common/components/RatioSelector';
import { Radius10Button } from '../../../common/styleds';
import BackForward from '../../../icons/BackForward';
import FastForward from '../../../icons/FastForward';
import PauseButton from '../../../icons/pauseButton';
import PlayButton from '../../../icons/PlayButton';
import { PlayerContext } from '../../type';
import { PlayButtons, Ratios, StyledPlayerControls } from './styled';

const playerControlButtonStyle = {
  background: '#1F2836',
  borderColor: '#222B3A',
};

type Props = {
  playerContext: PlayerContext;
};

const PlayerControls: React.FC<Props> = ({ playerContext }) => {
  const editorContextValue = useContext(editorContext);
  const { isPlaying, totalDuration, ratio, setRatio } = editorContextValue;
  const handlePlayingChange = useCallback(() => {
    playerContext.handleIsPlayingChange(!isPlaying);
  }, [playerContext, isPlaying]);
  const handleSetToStart = useCallback(() => {
    playerContext.handleCurrentTimeChange(0);
  }, [playerContext]);
  const handleSetToEnd = useCallback(() => {
    playerContext.handleCurrentTimeChange(totalDuration);
  }, [playerContext, totalDuration]);
  return (
    <StyledPlayerControls>

      <Ratios>
        <RatioSelector value={ratio} onChange={setRatio} />
      </Ratios>

      <PlayButtons>
        <Radius10Button onClick={handleSetToStart} style={playerControlButtonStyle}>
          <BackForward />
        </Radius10Button>
        <Radius10Button style={{ width: 60, ...playerControlButtonStyle }} onClick={handlePlayingChange}>
          {
            isPlaying ? <PauseButton /> : <PlayButton />
          }
        </Radius10Button>
        <Radius10Button onClick={handleSetToEnd} style={playerControlButtonStyle}>
          <FastForward />
        </Radius10Button>
      </PlayButtons>

      <Radius10Button style={{ width: 100 }} className="disabled">
        全屏播放
      </Radius10Button>

    </StyledPlayerControls>
  );
};

export default PlayerControls;
