import { noEffect } from './noEffect';
import { gray } from './gray';
import { Effect, EffectType } from '../../common/types/player';
import { ObjectType } from '../../common/types';
import { blur } from './blur';
import { ClipBase } from '../clips/base';

const effectMap: ObjectType<(clip?: ClipBase) => Effect> = {
  [EffectType.NO_EFFECT]: noEffect,
  [EffectType.GRAY]: gray,
  [EffectType.BLUR]: blur,
};

export default function effects(type?: EffectType) {
  return effectMap[type || EffectType.NO_EFFECT];
}
