import React, { useCallback } from 'react';
import { SelectorButton } from '../../styleds';
import { FlatSelectorDataItem } from './type';

type ItemProps = {
  value: any;
  data: FlatSelectorDataItem;
  onChange(data: any): void;
};

const SelectorItem: React.FC<ItemProps> = ({ data, value, onChange }) => {
  const handleClick = useCallback(() => {
    onChange(data.value);
  }, [data.value, onChange]);
  return (
    <SelectorButton
      onClick={handleClick}
      className={value === data.value ? 'active' : ''}
    >{data.label}</SelectorButton>
  );
};

type Props = {
  value: any;
  data: FlatSelectorDataItem[];
  onChange(data: any): void;
};

const FlatSelector: React.FC<Props> = ({
  data,
  value,
  onChange,
}) => {
  const handleChange = useCallback((value: any) => {
    onChange(value);
  }, [onChange]);
  return (
    <div>
      {
        data.map((item) => {
          return (
            <SelectorItem
              onChange={handleChange}
              key={item.value}
              data={item}
              value={value}
            >{item.label}</SelectorItem>
          );
        }, [])
      }
    </div>
  );
};

export default FlatSelector;
