import React, { useEffect } from 'react';
import { IMAGE_DEFAULT_DURATION } from '../../../../../common/configs/clip';
import { StyledAssetCard } from '../../../../../common/styleds';
import { ImageClip } from '../../../../../common/types/clip';

type Props = {
  data: ImageClip;
  onLoaded(data: ImageClip): void;
};

const ImageCard: React.FC<Props> = ({ data, onLoaded }) => {
  useEffect(() => {
    if (!data.blobUrl) {
      fetch(data.url).then((response) => {
        return response.blob();
      }).then((file: Blob) => {
        const blobUrl = URL.createObjectURL(file);
        const img = document.createElement('img');
        img.addEventListener('load', () => {
          onLoaded({
            ...data,
            positionInTimeline: {
              ...data.positionInTimeline,
              duration: IMAGE_DEFAULT_DURATION,
            },
            positionInRender: {
              ...data.positionInRender,
              width: img.naturalWidth,
              height: img.naturalHeight,
            },
            originalWidth: img.naturalWidth,
            originalHeight: img.naturalHeight,
            blobUrl,
          });
        });
        img.src = blobUrl;
      }).catch((error) => {
        alert(error.message);
      });
    }
  }, [data, onLoaded]);
  return (
    <StyledAssetCard>
      <img src={data.url} alt="" draggable="false" />
      <div className="title">{data.name}</div>
    </StyledAssetCard>
  );
};

export default ImageCard;
