import React, { useCallback } from 'react';
import ExportVideo from '../../../icons/ExportVideo';
import { exportAsVideo } from '../../../player/util';
import { PlayerContext } from '../../type';
import ConfigPanel from '../ConfigPanel';
import { ExportButton, StyledStageHeader } from './styled';

type Props = {
  playerContext: PlayerContext;
};

const StageHeader: React.FC<Props> = ({ playerContext }) => {
  const handleExportVideo = useCallback(() => {
    if (playerContext.player) {
      exportAsVideo(playerContext.player);
    }
  }, [playerContext.player]);
  return (
    <StyledStageHeader>
      <div>
        <ConfigPanel />
      </div>
      <div>未命名</div>
      <ExportButton onClick={handleExportVideo}>
        <ExportVideo />
        导出视频
      </ExportButton>
    </StyledStageHeader>
  );
};

export default StageHeader;
