import React, { useCallback, useContext } from 'react';
import { editorContext } from '../..';
import { Clip, ClipType } from '../../../common/types/clip';
import IndicatorBar from '../../../icons/IndicatorBar';
import { canWithTransition, cursorPositionToTracksWrapperPosition, getTotalDuration, getTrackAndClipIndex, isClipsNext, millisecondsToWidth, widthToMilliseconds } from '../../util';
import Track from './components/Track';
import TrackElement from './components/TrackElement';
import { StyledTrackElement } from './components/TrackElement/styled';
import { StyledIndicator, StyledTracks, StyledTracksWrapper } from './styled';

type Props = {
  onCurrentTimeChange(currentTime: number): void;
};

const Tracks: React.FC<Props> = ({ onCurrentTimeChange }) => {
  const { tracks, currentTime, draggingClip, setTracks } = useContext(editorContext);
  const trackStyle: React.CSSProperties = tracks.length === 1 && tracks[0].length === 0 ? { height: 90 } : {};

  const handleCurrentTimeChange = useCallback((x: number, y: number, width: number) => {
    const totalDuration = getTotalDuration(tracks);
    const pos = cursorPositionToTracksWrapperPosition({ x, y });
    const newCurrentTime = widthToMilliseconds(Math.max(0, pos.x - width));
    onCurrentTimeChange(Math.min(newCurrentTime, totalDuration));
  }, [onCurrentTimeChange, tracks]);

  const handleTracksWrapperClick = useCallback((e: React.MouseEvent) => {
    handleCurrentTimeChange(e.pageX, e.pageY, 0);
  }, [handleCurrentTimeChange]);

  const handleUpdate = useCallback((oldData: Clip, newData: Partial<Clip>, newTrackIndex: number, newClipIndex: number, isNewTrack: boolean) => {
    // 获取原数据所在索引
    const { trackIndex, clipIndex } = getTrackAndClipIndex(tracks, oldData.id);
    if (trackIndex > -1 && clipIndex > -1) {
      // 删除原clip
      const oldData = tracks[trackIndex].splice(clipIndex, 1)[0];
      Object.assign(oldData, newData);
      if (isNewTrack) {
        tracks.splice(newTrackIndex, 0, [oldData]);
      } else {
        tracks[newTrackIndex].splice(newClipIndex, 0, oldData);
      }
      setTracks([...tracks]);
    }
  }, [setTracks, tracks]);

  const isDraggingTransition = draggingClip?.type === ClipType.TRANSITION;
  const flatClips = tracks.flat(1);

  return (
    <>
      <StyledTracksWrapper className="tracks-wrapper" onClick={handleTracksWrapperClick}>
        <StyledTracks
          // style={{ top: `calc(50% - ${tracks.length * 35}px)` }}
        >
          {
            tracks.map((clips, index) => {
              return (
                <Track index={index} clips={clips} key={index} style={trackStyle} />
              );
            })
          }
          {
            flatClips.map((clip, index) => {
              const { trackIndex } = getTrackAndClipIndex(tracks, clip.id);
              const nextClip = flatClips[index + 1];
              let showTransitionPrepareInsert = false;
              if (isDraggingTransition && isClipsNext(clip, nextClip)) {
                if (canWithTransition(clip?.type) && canWithTransition(nextClip?.type)) {
                  showTransitionPrepareInsert = true;
                }
              }
              return (
                <React.Fragment key={clip.id}>
                  <TrackElement
                    trackIndex={trackIndex}
                    clip={clip}
                    onUpdate={(data: Partial<Clip>, newTrackIndex: number, newClipIndex: number, isNewTrack: boolean) => {
                      handleUpdate(clip, data, newTrackIndex, newClipIndex, isNewTrack);
                    }}
                  />
                  {
                    showTransitionPrepareInsert && draggingClip ? <StyledTrackElement
                      className="transition-insert"
                      data-from={clip.id}
                      data-to={nextClip.id}
                      style={{
                        width: millisecondsToWidth(draggingClip.positionInTimeline.duration),
                        left: millisecondsToWidth(clip.positionInTimeline.end - draggingClip.positionInTimeline.duration / 2),
                        zIndex: 999,
                        opacity: 0.5,
                        border: '2px solid #fff',
                      }}
                    /> : null
                  }
                </React.Fragment>
              );
            })
          }
        </StyledTracks>
      </StyledTracksWrapper>
      <StyledIndicator style={{ left: millisecondsToWidth(currentTime) + 20 }}>
        <IndicatorBar className="indicator-bar" />
      </StyledIndicator>
    </>
  );
};

export default Tracks;
