import {
  AudioClip,
  AudioMergeItem,
  Clip,
  ClipType,
  GifClip,
  ImageClip,
  LottieClip,
  SvgClip,
  TextClip,
  TransitionClip,
  VideoClip,
} from '../common/types/clip';
import { downloadFile, sleep } from '../common/utils';
import { Audio } from './clips/audio';
import { Image } from './clips/image';
import { Lottie } from './clips/lottie';
import { Svg } from './clips/svg';
import { Text } from './clips/text';
import { Transition } from './clips/transition';
import { Video } from './clips/video';
import Player from '.';
import FFmpegHelper from '../ffmpeg';
import { Gif } from './clips/gif';
import { FitType } from '../common/types/player';

export function createClip(config: Clip, player: Player) {
  switch (config.type) {
    case ClipType.VIDEO:
      return new Video(config as VideoClip, player);
    case ClipType.IMAGE:
      return new Image(config as ImageClip, player);
    case ClipType.TRANSITION:
      return new Transition(config as TransitionClip, player);
    case ClipType.TEXT:
      return new Text(config as TextClip, player);
    case ClipType.AUDIO:
      return new Audio(config as AudioClip, player);
    case ClipType.SVG:
      return new Svg(config as SvgClip, player);
    case ClipType.LOTTIE:
      return new Lottie(config as LottieClip, player);
    case ClipType.GIF:
      return new Gif(config as GifClip, player);
  }
}

export function canvasToArrayBuffer(canvas: HTMLCanvasElement) {
  return new Promise<ArrayBuffer>((resolve) => {
    canvas.toBlob(async (blob) => {
      if (blob) {
        const arrayBuffer = await blob.arrayBuffer();
        resolve(arrayBuffer);
      }
    }, 'image/jpeg', 1.0);
  });
}

async function generateVideo(player: Player, fps: number) {
  const interval = Math.round(1000 / fps);
  let frames: ArrayBuffer[] = [];
  let currentTime = 0;
  while (currentTime <= player.duration) {
    await player.setCurrentTime(currentTime);
    const imageData = await canvasToArrayBuffer(player.painter.getDomElement());
    frames.push(imageData);
    await sleep(10);
    currentTime += interval;
  }
  if (currentTime > player.duration) {
    player.setCurrentTime(player.duration);
    const imageData = await canvasToArrayBuffer(player.painter.getDomElement());
    frames.push(imageData);
  }
  const data = await FFmpegHelper.ImagesToVideo(frames, fps);
  frames.length = 0; // 清空图片
  return data;
}

function getAudioMergeItemFromPlayer(player: Player) {
  const result: AudioMergeItem[] = [];
  player.clips.forEach((clip) => {
    if ([ClipType.AUDIO, ClipType.VIDEO].includes(clip.config.type)) {
      result.push({
        delay: clip.config.positionInTimeline.start,
        from: 0,
        to: (clip as Video).getOriginalDuration(),
        tempo: (clip as Video).playbackRate,
        type: clip.config.type,
        buffer: (clip as Video).config.buffer.slice(0),
      });
    }
  });
  return result;
}

async function generateAudio(player: Player) {
  const audioMergeItems = getAudioMergeItemFromPlayer(player);
  if (audioMergeItems.length) {
    return await FFmpegHelper.MergeAudios(audioMergeItems);
  }
  return;
}

/**
 * 将player导出为h264视频文件
 */
export async function exportAsVideo(player: Player, fileName = 'export.mp4', fps = 30) {
  try {
    const videoBuffer = await generateVideo(player, fps).catch((error) => {
      alert(`video: ${error}`);
    });
    await sleep(100);
    const audioBuffer = await generateAudio(player).catch((error) => {
      alert(`audio: ${error}`);
    });
    await sleep(100);
    let finalVideoBuffer: ArrayBuffer;
    if (videoBuffer instanceof ArrayBuffer && audioBuffer instanceof ArrayBuffer) {
      finalVideoBuffer = await FFmpegHelper.MergeVideoAudio(videoBuffer, audioBuffer);
    } else if (videoBuffer instanceof ArrayBuffer) {
      finalVideoBuffer = videoBuffer;
    } else {
      return;
    }
    downloadFile(URL.createObjectURL(new Blob([finalVideoBuffer], { type: 'video/mp4' })), fileName);
  } catch (error) {
    alert(error);
  }
}

/**
 * 获取clip渲染相关的参数
 */
export function getClipRenderInfo(clip: Clip, playerWidth: number, playerHeight: number) {
  const { originalWidth, originalHeight } = clip;
  const {
    fitType,
    width: currentWidth,
    height: currentHeight,
    left: currentLeft,
    bottom: currentBottom,
  } = clip.positionInRender;
  let texWidth = currentWidth; // FitType.CUSTOM
  let texHeight = currentHeight; // FitType.CUSTOM
  let left = currentLeft; // FitType.CUSTOM
  let bottom = currentBottom; // FitType.CUSTOM
  if (fitType === FitType.CONTAIN) {
    const playerRatio = playerWidth / playerHeight;
    const texRatio = originalWidth / originalHeight;
    if (playerRatio / texRatio > 1) {
      texHeight = playerHeight;
      texWidth = originalWidth * (playerHeight / originalHeight);
      left = (playerWidth - texWidth) * 0.5;
      bottom = 0;
    } else {
      texWidth = playerWidth;
      texHeight = originalHeight * (playerWidth / originalWidth);
      left = 0;
      bottom = (playerHeight - texHeight) * 0.5;
    }
  } else if (fitType === FitType.COVER) {
    const playerRatio = playerWidth / playerHeight;
    const texRatio = originalWidth / originalHeight;
    if (playerRatio / texRatio > 1) {
      texWidth = playerWidth;
      texHeight = originalHeight * (playerWidth / originalWidth);
      left = 0;
      bottom = (playerHeight - texHeight) * 0.5;
    } else {
      texHeight = playerHeight;
      texWidth = originalWidth * (playerHeight / originalHeight);
      left = (playerWidth - texWidth) * 0.5;
      bottom = 0;
    }
  } else if (fitType === FitType.FILL) {
    texWidth = playerWidth;
    texHeight = playerHeight;
    left = 0;
    bottom = 0;
  }
  return { width: texWidth, height: texHeight, left, bottom };
}
