/**
 * 组装transition和effect
 */
import { FitType } from '../../common/types/player';
import { glsl } from 'typed-glsl';
import * as THREE from 'three';

type EffectProps = {
  effect: string;
};

type TransitionProps = {
  fromEffect: string;
  toEffect: string;
  transition: string;
};

/**
 * 根据适配模式计算uv映射
 */
export function calcUVMap(
  fitType: FitType,
  /** 播放器宽度 */
  playerWidth: number,
  /** 播放器高度 */
  playerHeight: number,
  /** width in positionInRender */
  currentWidth: number,
  /** height in positionInRender */
  currentHeight: number,
  /** 素材原始宽度 */
  originalWidth: number,
  /** 素材原始高度 */
  originalHeight: number,
  /** 相对于左下角的移动距离 */
  translateInPixel: THREE.Vector2 = new THREE.Vector2(0, 0),
) {
  let texWidth = currentWidth; // FitType.CUSTOM
  let texHeight = currentHeight; // FitType.CUSTOM
  let translate = translateInPixel.clone(); // FitType.CUSTOM
  if (fitType === FitType.CONTAIN) {
    const playerRatio = playerWidth / playerHeight;
    const texRatio = originalWidth / originalHeight;
    if (playerRatio / texRatio > 1) {
      texHeight = playerHeight;
      texWidth = originalWidth * (playerHeight / originalHeight);
      translate.set((playerWidth - texWidth) * 0.5, 0);
    } else {
      texWidth = playerWidth;
      texHeight = originalHeight * (playerWidth / originalWidth);
      translate.set(0, (playerHeight - texHeight) * 0.5);
    }
  } else if (fitType === FitType.COVER) {
    const playerRatio = playerWidth / playerHeight;
    const texRatio = originalWidth / originalHeight;
    if (playerRatio / texRatio > 1) {
      texWidth = playerWidth;
      texHeight = originalHeight * (playerWidth / originalWidth);
      translate.set(0, (playerHeight - texHeight) * 0.5);
    } else {
      texHeight = playerHeight;
      texWidth = originalWidth * (playerHeight / originalHeight);
      translate.set((playerWidth - texWidth) * 0.5, 0);
    }
  } else if (fitType === FitType.FILL) {
    texWidth = playerWidth;
    texHeight = playerHeight;
    translate.set(0, 0);
  }
  // + 0 可将 -0 变为 0
  const uRange = new THREE.Vector2(-translate.x / texWidth + 0, (playerWidth - translate.x) / texWidth);
  const vRange = new THREE.Vector2(-translate.y / texHeight + 0, (playerHeight - translate.y) / texHeight);
  return [uRange, vRange];
}

export class Shader {
  static GetVertex() {
    const vertexTemplate = glsl`
      varying vec2 texCoords;
      void main() {
        texCoords = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
      }
    `;
    return vertexTemplate;
  }

  static GetFragment() {
    const fragTemplate = glsl`
      varying vec2 texCoords;
      uniform sampler2D tex;
      void main() {
        gl_FragColor = texture2D(tex, texCoords);
      }
    `;
    return fragTemplate;
  }

  static GetEffectFragment({ effect }: EffectProps) {
    const fragTemplate = glsl`
      varying vec2 texCoords;
      uniform vec2 uRange;
      uniform vec2 vRange;
      uniform sampler2D tex;
      ${effect}
      vec4 getColor(vec2 uv) {
        vec2 newUV = vec2(uRange.x + uv.x * (uRange.y - uRange.x), vRange.x + uv.y * (vRange.y - vRange.x));
        vec4 color = effect(newUV);
        if(newUV.x>1. || newUV.x<0. || newUV.y>1. || newUV.y<0.){
          return vec4(0.,0.,0.,0.);
        }
        return color;
      }
      void main() {
        gl_FragColor = getColor(texCoords);
      }
    `;
    return fragTemplate;
  }

  static GetTransitionFragment({
    fromEffect,
    toEffect,
    transition,
  }: TransitionProps) {
    const fragTemplate = glsl`
      varying vec2 texCoords;
      uniform sampler2D from, to;
      uniform vec2 fromURange, fromVRange;
      uniform vec2 toURange, toVRange;
      uniform float fromRatio, toRatio;
      uniform float progress;
      ${fromEffect}
      ${toEffect}
      vec4 getFromColor(vec2 uv) {
        vec2 newUV = vec2(fromURange.x + uv.x * (fromURange.y - fromURange.x), fromVRange.x + uv.y * (fromVRange.y - fromVRange.x));
        vec4 color = fromEffect(newUV);
        if(newUV.x>1. || newUV.x<0. || newUV.y>1. || newUV.y<0.){
          return vec4(0.,0.,0.,0.);
        }
        return color;
      }
      vec4 getToColor(vec2 uv) {
        vec2 newUV = vec2(toURange.x + uv.x * (toURange.y - toURange.x), toVRange.x + uv.y * (toVRange.y - toVRange.x));
        vec4 color = toEffect(newUV);
        if(newUV.x>1. || newUV.x<0. || newUV.y>1. || newUV.y<0.){
          return vec4(0.,0.,0.,0.);
        }
        return color;
      }
      ${transition}
      void main() {
        gl_FragColor = transition(texCoords);
      }
    `;
    return fragTemplate;
  }
}
