import { TransitionType } from './player';

export enum AssetType {
  VIDEO,
  IMAGE,
  AUDIO,
  TRANSITION,
  GIF,
  TEXT,
  LOTTIE,
}

export interface Asset {
  name: string;
  type: AssetType;
}

export interface VideoAsset extends Asset {
  url: string;
}

export interface ImageAsset extends Asset {
  url: string;
}

export interface AudioAsset extends Asset {
  url: string;
}

export interface GifAsset extends Asset {
  url: string;
}

export interface TextAsset extends Asset {
  value: string;
}

export interface TransitionAsset extends Asset {
  transition: TransitionType;
}

export interface LottieAsset extends Asset {
  url: string;
}
