interface AsyncQueueItem {
  action: (...args: any[]) => Promise<any>;
  resolve: (value: unknown) => void;
  reject: (reason?: any) => void;
}

export class Queue<T> {
  private items: T[] = [];
  public enqueue(item: T) {
    this.items.push(item);
  }
  public dequeue() {
    return this.items.shift();
  }
  public get size() {
    return this.items.length;
  }
}

export class AsyncAutoQueue {
  private items: AsyncQueueItem[] = [];
  private pendingPromise: boolean = false;

  public enqueue<T>(action: () => Promise<T>) {
    return new Promise((resolve, reject) => {
      this.items.push({ action, resolve, reject });
      if (!this.pendingPromise) {
        this.dequeue();
      }
    }).then((result) => {
      return result;
    }).catch((error) => {
      throw new Error(error);
    }).finally(() => {
      this.pendingPromise = false;
      this.dequeue();
    });
  }

  public async dequeue() {
    const item = this.items.shift();
    if (!item) {
      return false;
    }
    try {
      this.pendingPromise = true;
      const payload = await item.action();
      item.resolve(payload);
    } catch (e) {
      item.reject(e);
    }
    return true;
  }
}
