import React, { useMemo } from 'react';
import { ImageClip } from '../../../../../../../common/types/clip';

type Props = {
  clip: ImageClip;
};

const ImageTrack: React.FC<Props> = ({ clip }) => {
  const style = useMemo(() => {
    return {
      position: 'absolute',
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      background: `url(${clip.blobUrl})`,
      backgroundRepeat: 'repeat',
      backgroundSize: 'contain',
    } as React.CSSProperties;
  }, [clip.blobUrl]);
  return (
    <div style={{ width: '100%' }}>
      <div style={style}></div>
    </div>
  );
};

export default ImageTrack;
