import React from 'react';
import { ASSET } from '../../../../../asset';
import { AssetCardContainer, AssetWhole } from '../../../../../common/styleds';
import AssetCard from '../../../AssetCard';

const TransitionPanel: React.FC = () => {
  return (
    <>
      {
        ASSET.transitions.map((item) => {
          return (
            <AssetWhole key={item.name}>
              <AssetCardContainer>
                <AssetCard data={item} />
              </AssetCardContainer>
              <div className="asset-title">{item.name}</div>
            </AssetWhole>
          );
        })
      }
    </>
  );
};

export default TransitionPanel;
